import * as requests from './requests';
import {config} from './../config';

let user = {}, preferences = {};

export function getAuthUser()
{
	return requests.getAuth(config.services.user + 'auth/user');
}

export function getPreferences()
{
	return requests.getAuth(config.services.user + 'user/preferences');
}

export function updatePreferences(preferences)
{
	return requests.putAuth(config.services.user + 'user/preferences', preferences);
}

export function setUserInfo(userInfo)
{
	user = userInfo;
	localStorage.setItem('userInfo', JSON.stringify(userInfo));
}

export function getUserInfo()
{
	return user;
}

export function setUserPreferences(pref)
{
	preferences = pref;
	localStorage.setItem('userPref', JSON.stringify(pref));
}

export function getUserPreferences()
{
	return preferences;
}

export function signIn(data)
{
	return requests.post(config.services.user + 'auth/login', data);
}

export function signInWithSocial(data)
{
	return requests.post(config.services.user + 'auth/login-with-social', data);
}

export function updateName(name, surname)
{
	return requests.postAuth(config.services.user + 'user/name/'+name+'/'+surname+'/', undefined);
}

export function updatePassword(password)
{
	return requests.postAuth(config.services.user + 'auth/set-new-password/' + password, undefined);
}

export function updateAvatar(avatar)
{
	return requests.postAuth(config.services.user + 'user/avatar/' + avatar, undefined);
}

export function updateStatus(status)
{
	user.appStatus = status;
	let data = {status: status};
	return requests.putAuth(config.services.user + 'user/status', data);
}
