import * as requests from "./requests";
import * as userService from "./users";
import * as teamService from "./teams";
import { config } from "./../config";
const electron = window.require("electron").remote;

let es,
  activeUsers = [],
  lastMessageUsers = [];

export function connect() {
  es = new EventSource(
    config.services.messaging + `notification/feed?token=` + requests.getToken()
  );
  console.log("event source: ", es);
  es.onerror = (err) => {
    console.log("error: ", err);
    setTimeout(() => {
      es.close();
      connect();
    }, 400);
  };
  es.onmessage = (event) => {
    let data = JSON.parse(event.data);
    let userInfo = userService.getUserInfo();
    
    console.log("add message event 222: ", data, userInfo);
    window.dispatchEvent(
      new CustomEvent("new-notification", {
        detail: data,
      })
    );
    window.dispatchEvent(
      new CustomEvent("new-notification-for-header", {
        detail: data,
      })
    );
    switch (parseInt(data.type)) {
      case 1:
        //console.log("active users: ", data.data);
        if (!data.data.code && data.data.users) {
          activeUsers = [];
          activeUsers.push(...data.data.users);
        } else if (data.data.code == "ntf.ht.requestAccepted" && data.data.actingUser.id != userInfo.id) {
          
          let preferences = userService.getUserPreferences();
          if (preferences && preferences.notifyJoinTeam) {
            let notiOptions = { body: "" };
            notiOptions.body =
              data.data.actingUser.name.first +
              " " +
              data.data.actingUser.name.last +
              " joined to the team";
            if (!preferences || !preferences.notificationSound)
              notiOptions.silent = true;

            const myNotification = new Notification(
              "New team member",
              notiOptions
            );
          }
        } else if (data.data.code == "ntf.meeting.newMember") {
          window.dispatchEvent(
            new CustomEvent("new-room", {
              detail: data.data.code,
            })
          );
          console.log('new member: ', data.data.actingUser.id, userInfo.id);
          if(data.data && data.data.actingUser && data.data.actingUser.id != userInfo.id){
            let preferences = userService.getUserPreferences();
            console.log('preferences: ', preferences);
            let notiOptions = { body: "" };

              notiOptions.body =
                data.data.actingUser.name.first;
              
              if(data.data.actingUser.name.last && data.data.actingUser.name.last.length){
                notiOptions.body += " " + data.data.actingUser.name.last;
              }

                notiOptions.body += " joined to the room " +
                data.data.room.title;

              if (!preferences || !preferences.notificationSound)
                notiOptions.silent = true;

              console.log('new noti: ', notiOptions);  

              const myNotification = new Notification(
                "New participant",
                notiOptions
              );
          }
          
        } else if (data.data.code == "ntf.meeting.memberLeft") {
          window.dispatchEvent(
            new CustomEvent("new-room", {
              detail: data.data.code,
            })
          );

          if(data.data && data.data.actingUser && data.data.actingUser.id != userInfo.id){
            let preferences = userService.getUserPreferences();
          
            let notiOptions = { body: "" };
              notiOptions.body =
                data.data.actingUser.name.first;
                if(data.data.actingUser.name.last && data.data.actingUser.name.last.length){
                  notiOptions.body += " " + data.data.actingUser.name.last;
                }
                
                notiOptions.body +=  " left the room " +
                data.data.room.title;
              if (!preferences || !preferences.notificationSound)
                notiOptions.silent = true;

              const myNotification = new Notification(
                "Participant left",
                notiOptions
              );
          }          
        }

        break;
      case 7:
        console.log("notification data: ", data.data);
        if (data.data.code == "user.video-call") {
          let id = data.data.otherUser.id,
            fromId = data.data.actingUser.id;

          // let temp = '<div class="cd-popup is-visible" id="video-call" role="alert"><div class="cd-popup-container">' +
          // '<p>'+data.data.actingUser.name.first+' calling...</p><ul class="cd-buttons">' +
          // '<li><a href="javscript:void(0)" id="acceptCall">Accept</a></li><li><a href="javscript:void(0)" id="rejectCall">Reject</a></li></ul>' +
          // '</div></div>';

          // document.body.innerHTML = document.body.innerHTML + temp;
          // listenOnAcceptReject(id, fromId);

          let path = electron.getGlobal("sharedPath");
          let filePath = electron.getGlobal("filePath");

          const BrowserWindow = electron.BrowserWindow;
          let callWindow = new BrowserWindow({
            width: 300,
            height: 533,
            title: "Wteam call",
            frame: false,
            transparent: true,
            resizable: false,
            fullscreen: false,
            webPreferences: {
              preload: filePath + "../public/preload.js",
              nodeIntegration: true,
              enableRemoteModule: true,
              webSecurity: false,
            },
          });
          callWindow.setFullScreenable(true);
          callWindow.loadURL(
            path +
              "../call/call.html?roomId=" +
              id +
              "&fromId=" +
              fromId +
              "&token=" +
              requests.getToken()
          );
        } else if (data.data.code == "user.audio-call") {
          let id = data.data.otherUser.id,
            fromId = data.data.actingUser.id;

          let path = electron.getGlobal("sharedPath");
          let filePath = electron.getGlobal("filePath");

          const BrowserWindow = electron.BrowserWindow;
          let callWindow = new BrowserWindow({
            width: 300,
            height: 533,
            title: "Wteam call",
            frame: false,
            transparent: true,
            resizable: false,
            fullscreen: false,
            webPreferences: {
              preload: filePath + "../public/preload.js",
              nodeIntegration: true,
              enableRemoteModule: true,
              webSecurity: false,
            },
          });
          callWindow.setFullScreenable(true);
          callWindow.loadURL(
            path +
              "../call/call.html?onlyAudio=true&roomId=" +
              id +
              "&fromId=" +
              fromId +
              "&token=" +
              requests.getToken()
          );
        } else if (data.data.code == "user.request-share") {
          let id = data.data.otherUser.id,
            fromId = data.data.actingUser.id;

          const myNotification = new Notification("Share request", {
            body:
              data.data.actingUser.name.first + " requested share your screen",
          });

          myNotification.onclick = () => {
            let user = userService.getUserInfo();
            user.uType = user.type;
            let noti = {
              code: "user.video-call",
              actingUser: user,
              otherUser: { id: fromId },
              involved: [fromId],
            };

            addNotification(noti)
              .then((res) => res.json())
              .then((data) => {
                let path = electron.getGlobal("sharedPath");
                let filePath = electron.getGlobal("filePath");

                const BrowserWindow = electron.BrowserWindow;
                let callWindow = new BrowserWindow({
                  width: 300,
                  height: 533,
                  title: "Wteam call",
                  frame: false,
                  transparent: true,
                  resizable: false,
                  fullscreen: false,
                  webPreferences: {
                    preload: filePath + "../public/preload.js",
                    nodeIntegration: true,
                    enableRemoteModule: true,
                    webSecurity: false,
                  },
                });
                callWindow.setFullScreenable(true);
                callWindow.loadURL(
                  path +
                    "../call/call.html?roomId=" +
                    fromId +
                    "&fromId=" +
                    user.id +
                    "&token=" +
                    requests.getToken()
                );
              })
              .catch((err) => {
                console.log("members error: ", err);
              });
          };
        }
        break;
      case 2:
        console.log("new message: ", data.data);

        if (data.data.fromId != userInfo.id && !data.data.roomId) {
          let teamMembers = teamService.getMembers();
          let member = teamMembers.find((m) => m.id == data.data.fromId);
          let title = "New message";
          if (member) {
            title += " from " + member.name.first + " " + member.name.last;
          }
          setLastMessageUsers([data.data.fromId]);
          let preferences = userService.getUserPreferences();
          let notiOptions = { body: data.data.message };
          if (!preferences || !preferences.notificationSound)
            notiOptions.silent = true;

          const myNotification = new Notification(title, notiOptions);

          myNotification.onclick = () => {
            let fromId = userService.getUserInfo().id,
              id = data.data.fromId;
            let path = electron.getGlobal("sharedPath");
            let filePath = electron.getGlobal("filePath");

            const BrowserWindow = electron.BrowserWindow;
            let chatWindow = new BrowserWindow({
              width: 300,
              height: 500,
              title: "Wteam chat",
              frame: false,
              transparent: true,
              resizable: false,
              fullscreen: false,
              webPreferences: {
                preload: filePath + "../public/preload.js",
                nodeIntegration: true,
                enableRemoteModule: true,
                webSecurity: false,
                devTools: false,
              },
            });
            chatWindow.loadURL(
              path +
                "../chat/mainc.html?fromId=" +
                fromId +
                "&toId=" +
                id +
                "&token=" +
                requests.getToken()
            );
          };
        }
        break;
    }
  };
}

export function disconnect() {
  if (es) {
    es.close();
  }
  activeUsers = [];
  lastMessageUsers = [];
}

export function getES() {
  return es;
}

export function getActiveUsers() {
  return activeUsers;
}

export function getUnreadMessagesUsers() {
  return requests.getAuth(config.services.messaging + "unread-messages-users");
}

export function getLastMessageUsers() {
  return lastMessageUsers;
}

export function setLastMessageUsers(users) {
  lastMessageUsers.push(...users);
  lastMessageUsers = lastMessageUsers.filter((el, i) => {
    return lastMessageUsers.indexOf(el) == i;
  });
  window.dispatchEvent(new CustomEvent("last-message-users"));
}

export function removeLastMessageUser(id) {
  let index = lastMessageUsers.indexOf(id);
  if (index != -1) lastMessageUsers.splice(index);

  window.dispatchEvent(new CustomEvent("last-message-users"));
}

export function addNotification(data) {
  return requests.postAuth(config.services.messaging + "notification", data);
}

export function readNotification(id) {
  return requests.putAuth(
    config.services.messaging + "notification/" + id + "/read",
    undefined
  );
}

export function getNotifications() {
  return requests.getAuth(config.services.messaging + "notifications");
}

