import React, { useState, useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";
import person from "../../../assets/image/other/person.png";
import "./Status.css";
import StatusItem from "./Statusitem";
import StatusWrite from "./StatusWrite";
import icons from "../../../assets/image/icons.svg";
import * as requests from "../../../api/requests";
import * as userService from "../../../api/users";
import * as filesService from "../../../api/files";
import * as messagingService from "../../../api/messaging";

function Status() {
  const statuses = [
    "Active",
    "Focus mode",
    "Getting lunch",
    "In meeting",
    "Childcare",
  ];
  const [status, setStatus] = useState(statuses[0]);
  const [open, setOpen] = useState(false);
  const [avatarurl, setAvatarurl] = useState("");
  const [name, setName] = useState("");
  const [lastname, setLastname] = useState("");
  const ref = useRef(null);
  let history = useHistory();

  useEffect(() => {
    function handleClickOutside(event) {
      if (
        ref.current &&
        !ref.current.contains(event.target) &&
        !event.target.closest(".emoji-picker__wrapper")
      ) {
        setOpen(false);
      }
    }
    document.addEventListener("mouseup", handleClickOutside);
    return () => {
      document.removeEventListener("mouseup", handleClickOutside);
    };
  }, [ref]);

  useEffect(() => {
    let user = userService.getUserInfo();
    if (user && user.name) {
      setName(user.name.first);
      setName(user.name.last);
      if (user.avatar && !avatarurl) {
        let url = filesService.getFileUrl(user.avatar);
        if (url) {
          setAvatarurl(url);
        } else {
          filesService
            .getFile(user.avatar)
            .then((res) => res.json())
            .then((data) => {
              filesService.saveFile({ id: user.avatar, url: data });
              setAvatarurl(data);
              console.log("data: ", data);
            });
        }
      }
    } else {
      setTimeout(() => {
        user = userService.getUserInfo();
        if (user && user.name) {
          setName(user.name.first);
          setName(user.name.last);
          if (user.avatar && !avatarurl) {
            let url = filesService.getFileUrl(user.avatar);
            if (url) {
              setAvatarurl(url);
            } else {
              filesService
                .getFile(user.avatar)
                .then((res) => res.json())
                .then((data) => {
                  filesService.saveFile({ id: user.avatar, url: data });
                  setAvatarurl(data);
                  console.log("data: ", data);
                });
            }
          }
        }
      }, 2500);
    }

    if (user.appStatus) {
      setStatus(user.appStatus);
    }
  }, []);

  function logout() {
    messagingService.disconnect();
    requests.setToken("");
    history.push("/signin");
  }

  function changeStatus(i) {
    setStatus(statuses[i]);

    userService
      .updateStatus(statuses[i])
      .then((res) => res.json())
      .then((data) => {
        let user = data;
        user.uType = user.type;
        let noti = {
          code: "user.status-updated",
          actingUser: user,
          appStatus: statuses[i],
        };
        messagingService
          .addNotification(noti)
          .then((res) => res.json())
          .then((data) => {});
      });
  }

  return (
    <div className="status" ref={ref}>
      <div className="status-body">
         {avatarurl && (
        <div className="status-toggle" onClick={() => setOpen(!open)}>
          <img src={avatarurl} alt="Person" />
        </div>
        )}
      {!avatarurl && (
        <div className="status-toggle" onClick={() => setOpen(!open)}>
          <p>
                  {name.charAt(0)}
                  {lastname.charAt(0)}
                </p>
        </div>
        )} 
    {/*    <div className="status-toggle" onClick={() => setOpen(!open)}>
          <img src={person} alt="Person" />
        </div>*/}
        <div className={`status-content ${open ? "open" : ""}`}>
          <div className="status-content__body">
            <div className="status-content__head">
            { status == 'Focus mode' && (
              <span style={{marginLeft: "0px"}}>🙅‍♀️</span>
              )}
            { status == 'Active' && (
              <svg className="icon icon-circul active">
                            <use href={icons + "#icon-circul"}></use>
                          </svg>
              )}
            { status == 'Getting lunch' && (
              <span style={{marginLeft: "0px"}}>🍜</span>
              )}

            { status == 'In meeting' && (
              <span style={{marginLeft: "0px"}}>📆</span>
              )}

            { status == 'Childcare' && (
              <span style={{marginLeft: "0px"}}>🤱</span>
              )}
              <span>{status}</span>
            </div>

            <div className="status-custom">
              <div className="status-custom__head">
                <span>Custom status</span>
              </div>
              <div className="status-custom__center">
                <ul className="status-list">
                  <li className="status-item" ref={ref}>
                    <div className="status-item__content">
                      <div
                        className={`status-item__about close`}
                        onClick={() => changeStatus(0)}
                      >
                        <span>
                          <svg className="icon icon-circul active">
                            <use href={icons + "#icon-circul"}></use>
                          </svg>
                        </span>
                        <span className={`status-item__name`}>Active</span>
                      </div>
                    </div>
                  </li>
                  <li className="status-item" ref={ref}>
                    <div className="status-item__content">
                      <div
                        className={`status-item__about close`}
                        onClick={() => changeStatus(1)}
                      >
                        <span>🙅‍♀️</span>
                        <span className={`status-item__name`}>Focus mode</span>
                      </div>
                    </div>
                    <span className="status-item__text">
                      *Apps are not shared while in Focus mode and you dont
                      recive any call.
                    </span>
                  </li>
                  <li className="status-item" ref={ref}>
                    <div className="status-item__content">
                      <div
                        className={`status-item__about close`}
                        onClick={() => changeStatus(2)}
                      >
                        <span>🍜</span>
                        <span className={`status-item__name`}>
                          Getting lunch
                        </span>
                      </div>
                    </div>
                  </li>
                  <li className="status-item" ref={ref}>
                    <div className="status-item__content">
                      <div
                        className={`status-item__about close`}
                        onClick={() => changeStatus(3)}
                      >
                        <span>📆</span>
                        <span className={`status-item__name`}>In meeting</span>
                      </div>
                    </div>
                  </li>
                  <li className="status-item" ref={ref}>
                    <div className="status-item__content">
                      <div
                        className={`status-item__about close`}
                        onClick={() => changeStatus(4)}
                      >
                        <span>🤱</span>
                        <span className={`status-item__name`}>Childcare</span>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
              {/*<div className="status-custom__bottom">
                <StatusWrite />
              </div>*/}
            </div>
            {/*<div className="logout-btn">
              <button className="submit" onClick={(e) => logout()}>Logout</button>
            </div>*/}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Status;
