import React, { useState, useMemo, useContext } from "react";
import "./InvitePeople.css";
import icons from "../../../assets/image/icons.svg";
import InvitePeopleItem from "./InvitePeopleItem";
import person from "../../../assets/image/other/person2.png";
import { InviteContext } from "../../../context/InvitePeopleContext";
import * as teamService from "../../../api/teams";
import * as filesService from "../../../api/files";
import * as meetingService from "../../../api/meeting";

function InvitePeople() {
  const { invitePeopleShow, showInviteModal } = useContext(InviteContext);

  const [result, setResult] = useState([]);
  //   const [addPeople, setAddPeople] = useState(invitePeopleShow.addPeople);
  const [search, setSearch] = useState("");

  const [participants, setParticipants] = useState(invitePeopleShow.participants);

  useMemo(() => {
    if (invitePeopleShow.addPeople) {
      let m = teamService.getMembers();
      m.forEach((i) => {
        let name = i.name && i.name.first ? i.name.first + ' ' + i.name.last : i.name;
        i.name = name;
        if(i.avatar){
          let url = filesService.getFileUrl(i.avatar);
          if (url) {
            i.avatarUrl = url;
          }
        }
        if(participants.find(p => p.userId == i.id)){
          i.add = true;
        }
      });
      console.log('members: ', m);
      setResult([
        ...m.filter((p) => {
          return (
            p.name.toLocaleLowerCase().indexOf(search.toLocaleLowerCase()) !==
            -1
          );
        }),
      ]);
    } else {
      setResult([
        ...participants.filter((p) => {
          return (
            p.add !== invitePeopleShow.addPeople &&
            p.name.toLocaleLowerCase().indexOf(search.toLocaleLowerCase()) !==
              -1
          );
        }),
      ]);
    }
  }, [invitePeopleShow.addPeople, search, participants]);

  function addMember(id, name, avatar, email){
    let member = {
      userId: id,
      name: name, 
      avatar: avatar,
      email: email
    };
    let parts = [...participants];
    parts.push(member);
    invitePeopleShow.participants.push(member);

    meetingService.updateMeetingMembers(invitePeopleShow.id, {attandee: parts})
                  .then((res) => res.json())
                  .then((data) => {
                    setParticipants(parts);
                  })
                  .catch((err) => {
                    console.log("rooms error: ", err);
                  });
  }

  function removeMember(id){
    let parts = [...participants];
    parts = parts.filter(p => p.userId != id);
    invitePeopleShow.participants = invitePeopleShow.participants.filter(p => p.userId != id);

    meetingService.updateMeetingMembers(invitePeopleShow.id, {attandee: parts})
                  .then((res) => res.json())
                  .then((data) => {
                    setParticipants(parts);
                  })
                  .catch((err) => {
                    console.log("rooms error: ", err);
                  });
  }

  return (
    <div className="invitepeople">
      <div className="invitepeople-body">
        <div className="invitepeople-modal">
          <div
            className="invitepeople-modal__close"
            onClick={() => showInviteModal(false, false)}
          >
            <svg className="icon icon-cancel">
              <use href={icons + "#icon-cancel"}></use>
            </svg>
          </div>
          <div className="invitepeople-header">
            <div className="invitepeople-header__emoji">
              <span role="img" aria-label={invitePeopleShow.icon}>
                {invitePeopleShow.icon}
              </span>
            </div>
            <div className="invitepeople-header__about">
              <h3 className="invitepeople-header__title">{invitePeopleShow.title}</h3>
              <span className="invitepeople-header__count">{invitePeopleShow.participants.length} members</span>
            </div>
          </div>
          { !invitePeopleShow.hideButton && 
          <div className="invitepeople-add">
            <button
              onClick={() => showInviteModal(true, !invitePeopleShow.addPeople, invitePeopleShow.id, invitePeopleShow.title, invitePeopleShow.icon, invitePeopleShow.participants, false)}
            >
              {invitePeopleShow.addPeople ? "See people" : "Add people"}
            </button>
          </div>
          }
          <div className="invitepeople-search">
            <input
              type="text"
              placeholder="Search members"
              onChange={(e) => setSearch(e.target.value)}
            />
            <svg className="icon icon-search">
              <use href={icons + "#icon-search"}></use>
            </svg>
          </div>
          <div className="invitepeople-men">
            <ul className="invitepeople-men__list">
              {result.map((r, i) => (
                <li className="invitepeople-men__item">
                  {r.avatarUrl && 
                    <img src={r.avatarUrl} alt={r.name} />
                  }
                  {!r.avatarUrl && 
                    <div className="invitepeople-men__item--name">{r.name.charAt(0)}</div>
                  }
                  <span>{r.name}</span>
                  {invitePeopleShow.addPeople ? (
                    r.add ? (
                      <button className="added" onClick={() => {removeMember(r.id)}}>
                        Added
                      </button>
                    ) : (
                      <button onClick={() => {addMember(r.id, r.name, r.avatar, r.email)}}>
                        Add
                      </button>
                    )
                  ) : (
                    ""
                  )}
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}

export default InvitePeople;
