import React from "react";
import { useHistory } from "react-router-dom";
import "./Aside.css";
import AsideNav from "./AsideNav";
import { Link } from "react-router-dom";
import logo from "../../assets/image/logo/Logo.png";
import icons from "../../assets/image/icons.svg";
import * as requests from '../../api/requests';
import * as messagingService from "../../api/messaging";
import * as userService from "../../api/users";
import * as teamService from "../../api/teams";

function Aside() {

  let history = useHistory();

  function logout() {
    messagingService.disconnect();
    requests.setToken('');
    userService.setUserInfo({});
    userService.setUserPreferences({});
    teamService.clearTeamService();
    history.push('/signin');
  }

  return (
    <aside className="aside">
      <div className="aside-body">
        <div className="aside-logo">
          <Link to="/dashboard">
            <img src={logo} alt="Logo" />
          </Link>
        </div>
        <AsideNav />
        <div className="aside-bottom">
          <a href="javascript:void(0)" onClick={(e) => logout()}>
            <svg className="icon icon-log-out">
              <use href={icons + "#icon-log-out"}></use>
            </svg>
          </a>
        </div>
      </div>
    </aside>
  );
}

export default Aside;
