import React, { useState, useEffect, useRef } from "react";
import icons from "../../assets/image/icons.svg";
import { Link, useHistory } from "react-router-dom";

function FolderItems() {
  const [open, setOpen] = useState(false);
  const ref = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setOpen(false);
      }
    }
    document.addEventListener("click", handleClickOutside);
    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, [ref]);

  const history = useHistory();

  const handleUrl = () => {
    history.push("/milstone")
  }

  return (
    <div className="folder-content" onClick={handleUrl}>
      <div className="folder-content__head">
        <h2>Office Manual</h2>
        <span role="img" aria-label="🔐">
          🔐
        </span>
        <button onClick={() => setOpen(!open)} ref={ref}>
          <svg className="icon icon-dost">
            <use href={icons + "#icon-dost"}></use>
          </svg>
        </button>
        {open && (
          <div className="folder-tools">
            <div className="folder-tools__body">
              <div className="folder-tools__content">
                <p onClick={() => setOpen(!open)}>
                  <Link to="/folder/create">Edit</Link>
                </p>
                <p onClick={() => setOpen(!open)}>Mute</p>
                <p onClick={() => setOpen(!open)}>Remove</p>
              </div>
            </div>
          </div>
        )}
      </div>
      <div className=""></div>
    </div>
  );
}

export default FolderItems;
