import React, { createContext, useState } from "react";

export const TableTheme = createContext();

export function TableProvider(props) {
  const [variant, setVariant] = useState("apps");
  const changeVariant = (v) => setVariant(v);
  return (
    <TableTheme.Provider value={{ variant, changeVariant }}>
      {props.children}
    </TableTheme.Provider>
  );
}
