import React, { useState, useEffect } from "react";
import icons from "../../../assets/image/icons.svg";
import * as filesService from "../../../api/files";

function Teammember({ person, name, lastname, admin, onMakeAdmin, editable, onDeleteMember }) {
  const [open, setOpen] = useState(false);
  const [avatarurl, setAvatarurl] = useState('');

  useEffect(() => {
    if(person && !avatarurl){
      let url = filesService.getFileUrl(person);
      if(url){
        setAvatarurl(url);
      } else {
        filesService.getFile(person).then((res) => res.json())
        .then((data) => {
          filesService.saveFile({id: person, url: data});
          setAvatarurl(data);
          console.log('data: ', data);
        });
      }
      
    }
  }, []);

  return (
    <div className="team-member">
      <div className="team-member__about">
        {avatarurl && ( 
        <img className="image" src={avatarurl} alt="Person" />
        )}
        {!avatarurl && ( 
          <div className="team-member__about__image--name">
                <p>
                  {name.charAt(0)}
                  {lastname.charAt(0)}
                </p>
              </div>
        )}
        <span className="name">{name}</span>
        {admin && <span className="admin">admin</span>}
        {editable && (
        <div className="team-tools" onClick={() => setOpen(!open)}>
          <div className="team-tools__button">
            <svg className="icon icon-dost">
              <use href={icons + "#icon-dost"}></use>
            </svg>
          </div>
          {open && (
            <div className="team-tools__content">
              <div className="team-tools__list">
                <div
                  className="team-tools__item"
                  onClick={() => {onMakeAdmin();setOpen(!open);}}
                >
                  <svg className="icon icon-person">
                    <use href={icons + "#icon-person"}></use>
                  </svg>
                  <span>Make admin</span>
                </div>
                <div
                  className="team-tools__item"
                  onClick={() => {onDeleteMember();setOpen(!open);}}
                >
                  <svg className="icon icon-delete">
                    <use href={icons + "#icon-delete"}></use>
                  </svg>
                  <span>Remove from team</span>
                </div>
              </div>
            </div>
          )}
        </div>
        )}
      </div>
    </div>
  );
}

export default Teammember;
