import React, { createContext, useState } from "react";

export const ActiveDropdown = createContext();

export function ActiveDropdownProvider(props) {
  const [aselect, setAselect] = useState("");
  const changeAselect = (v) => setAselect(v);
  return (
    <ActiveDropdown.Provider value={{ aselect, changeAselect }}>
      {props.children}
    </ActiveDropdown.Provider>
  );
}
