import React, { createContext, useState } from "react";

export const AddMeetingContext = createContext();

export function AddMeetingProvider(props) {
  const [addMeeting, setAddMeeting] = useState({
  	show: false,
  	attandee: []
  });
  const showAddMeeting = (show, id, title, emoji, attandee, startTime, endTime, preferences) => setAddMeeting({
  	show: show,
  	id: id,
  	title: title,
  	emoji: emoji,
  	attandee: attandee,
  	startTime: startTime,
  	endTime: endTime,
  	preferences: preferences
  });
  return (
    <AddMeetingContext.Provider value={{ addMeeting, showAddMeeting }}>
      {props.children}
    </AddMeetingContext.Provider>
  );
}
