import React, { useState, useEffect, useRef } from "react";
import "./Invite.css";
import Toastify from 'toastify-js';
import "toastify-js/src/toastify.css";
import icons from "../../../assets/image/icons.svg";
import * as requests from "../../../api/requests";
import * as teamsService from "../../../api/teams";
import * as userService from "../../../api/users";
import { CopyToClipboard } from "react-copy-to-clipboard";

function Invite() {
  const ref = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setShow(false);
      }
    }
    document.addEventListener("click", handleClickOutside);
    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, [ref]);

  const [show, setShow] = useState(false);
  const [send, setSend] = useState(false);
  const [email, setEmail] = useState("");
  const [inviteurl, setInviteurl] = useState("");
  const [copy, setCopy] = useState(false);
  let teamEventListener;

  useEffect(() => {
    let teamIdInterval = setInterval(() => {
      let team = teamsService.getCurrentTeam();
      if (team && team.id) {
        clearInterval(teamIdInterval);
        let url = "https://wteam.chat/auth/createteam?team=" + team.id;
        setInviteurl(url);
      }
    }, 1000);

    if (teamEventListener == undefined || teamEventListener == null) {
      teamEventListener = window.addEventListener("team-selected-invitation", function (
        event
      ) { 
          let team = teamsService.getCurrentTeam();
          let url = "https://wteam.chat/auth/createteam?team=" + team.id;
          setInviteurl(url);
      });
    }
  }, []);

  function copyToClipboard() {
    var copyText = document.getElementById("myInput");

    copyText.select();
    copyText.setSelectionRange(0, 99999);

    document.execCommand("copy");
  }

  function sendInvitation() {
    const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
    if (!send && pattern.test(email)) {
      teamsService
        .inviteMember(email)
        .then((res) => res.json())
        .then((data) => {
          setSend(!send);
          setEmail("");
        })
        .catch((err) => {
          console.log("members error: ", err);
          setSend(false);
        });
    } else {
      setSend(false);
    }
  }

  function addNoti(){
    Toastify({
      text: "Link copied!",
      duration: 3000,
      close: true,
      gravity: "bottom", // `top` or `bottom`
      position: "right", // `left`, `center` or `right`
      backgroundColor: "linear-gradient(to right, #00b09b, #96c93d)",
      stopOnFocus: true, // Prevents dismissing of toast on hover
    }).showToast();
  }

  return (
    <div className="invite" ref={ref}>
      <div className="invite-body">
        <button
          className={`invite-toggle ${show ? "open" : ""}`}
          onClick={() => setShow(!show)}
        >
          <svg className="icon icon-plus-circle ">
            <use href={icons + "#icon-plus-circle"}></use>
          </svg>
          <span>Invite Teammates</span>
        </button>
        <div className={`invite-content ${show ? "open" : ""}`}>
          <div className="invite-content__body">
            <div className="invite-close">
              <button
                className="invite-close__button"
                onClick={() => setShow(false)}
              >
                <svg className="icon icon-close">
                  <use href={icons + "#icon-close"}></use>
                </svg>
              </button>
            </div>
            <div className="invite-group">
              <label>Copy Link and Share</label>
              <div className="invite-group__input">
                <input id="myInput" type="text" value={inviteurl} disabled />
                <CopyToClipboard text={inviteurl} onCopy={() => {addNoti();}}>
                  <button>Copy</button>
                </CopyToClipboard>
              </div>
            </div>
            <div className="invite-group">
              <label>Or Invite with Email</label>
              <div className="invite-group__input">
                {send ? (
                  <input
                    type="text"
                    value={`You Have Successfully Invite ${email}`}
                    disabled
                  />
                ) : (
                  <input
                    type="text"
                    value={email}
                    placeholder="example@domain.com"
                    onChange={(e) => setEmail(e.target.value)}
                  />
                )}

                <button onClick={() => sendInvitation()}>
                  {send ? "Ok" : "Send"}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Invite;
