import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import StLayout from "../StLayout/StLayout";
import person from "../../../assets/image/other/person2.png";
import "../Settings.css";
import icons from "../../../assets/image/icons.svg";
import Layout from "../../../hoc/Layout/Layout";
import * as userService from "../../../api/users";
import * as fileService from "../../../api/files";

function Account() {
  const [image, setImage] = useState('');
  const [imageurl, setImageurl] = useState('');
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [time, setTime] = useState('');
  const [surname, setSurname] = useState('');
  const [password, setPassword] = useState('');

  let history = useHistory();

  useEffect(() => {
    let user = userService.getUserInfo();
    setName(user.name.first);
    setSurname(user.name.last);
    setEmail(user.email);

    let d = new Date();
    let timeString = (d.getHours() < 10 ? '0' + d.getHours() : d.getHours()) + ":" + (d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes());
    setTime(timeString);

    if(user.avatar){
      setImage(user.avatar);
      fileService.getFile(user.avatar).then((res) => res.json())
      .then((data) => {
        setImageurl(data);
      }).catch((err) => {
        console.log("members error: ", err);
      });
    }
  }, []);

  function uploadImage(file){
    console.log("got file: ", file);
    fileService.uploadFile(file).then((res) => res.json())
      .then((data) => {
        setImage(data.toString());
        fileService.getFile(data.toString()).then((res) => res.json())
        .then((data) => {
          setImageurl(data.toString());
        }).catch((err) => {
          console.log("members error: ", err);
        });
      }).catch((err) => {
        console.log("members error: ", err);
      });
  }

  function updateUser(event){
    event.preventDefault();
    if(name.trim().length && surname.trim().length){
      userService.updateName(name, surname).then((res) => res.json())
      .then((data) => {
        userService.updateAvatar(image).then((res) => res.json())
        .then((data) => {
          if(password.trim().length){
            userService.updatePassword(password).then((res) => res.json())
            .then((data) => {
              userService.getAuthUser().then(res => res.json())
                          .then(data => {
                            console.log('user data: ', data);
                            userService.setUserInfo(data);
                            history.push('/dashboard');
                          });
            });
          } else {
            userService.getAuthUser().then(res => res.json())
                          .then(data => {
                            console.log('user data: ', data);
                            userService.setUserInfo(data);
                            history.push('/dashboard');
                          });
          }
        });
      });
    }
  }

  return (
    <Layout>
      <StLayout page="My Account">
        <div className="settings-profile">
          <div className="settings-profile__picture">
            <label className="settings-profile__image">
              <input
                type="file"
                hidden
                onChange={(e) =>
                  uploadImage(e.target.files[0])
                }
              />
              { imageurl && (
                <img src={imageurl} alt="Person" />
              )}
              { !imageurl && (
                <p>{name.charAt(0)}{surname.charAt(0)}</p>
              )}
              <div className="settings-change__photo">
                <svg className="icon icon-camera">
                  <use href={icons + "#icon-camera"}></use>
                </svg>
              </div>
            </label>
            <div className="settings-profile__name">
              <h3>{name} {surname}</h3>
            </div>
          </div>
          <div className="settings-profile__details">
            <div className="settings-profile__details-content">
              <svg className="icon icon-mail">
                <use href={icons + "#icon-mail"}></use>
              </svg>
              <span>Email:</span>
              <span>{email}</span>
            </div>
            <div className="settings-profile__details-content">
              <svg className="icon icon-watch">
                <use href={icons + "#icon-watch"}></use>
              </svg>
              <span>Local time:</span>
              <span>{time}</span>
            </div>
          </div>
        </div>
        <div className="settings-form">
          <h3>Profile</h3>
          <form>
            <div className="settings-form__group">
              <label>First Name</label>
              <input type="text" value={name} onChange={(e) => setName(e.target.value)} />
            </div>
            <div className="settings-form__group">
              <label>Last Name</label>
              <input type="text" value={surname} onChange={(e) => setSurname(e.target.value)} />
            </div>
            <div className="settings-form__group">
              <label>Password</label>
              <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
            </div>
            <div className="settings-form__submit">
              <button onClick={(e) => updateUser(e)}>Save</button>
            </div>
          </form>
        </div>
        {/* <div className="settings-form__info">
          <p>Click your profile picture above to update</p>
        </div> */}
      </StLayout>
    </Layout>
  );
}

export default Account;
