import React, { useContext, useRef, useEffect, useState } from "react";
import { useHistory, Link } from "react-router-dom";
import Dropdown from "../../components/UI/Dropdown/Dropdown";
import Invite from "../../components/UI/Invite/Invite";
import InvitePeople from "../../components/UI/InvitePeople/InvitePeople";
import AddMeeting from "../../container/Meeting/AddMeeting/AddMeeting";
import icons from "../../assets/image/icons.svg";
import { SearchTheme } from "../../context/SearchContext";
import { ActiveDropdown } from "../../context/ActiveDropdownContext";
import { TimeDropdown } from "../../context/TimeDropdownContext";
import { TableTheme } from "../../context/TableVariant";
import { InviteContext } from "../../context/InvitePeopleContext";
import { AddMeetingContext } from "../../context/AddMeetingContext";
import Header from "../../components/Header/Header";
import { EmojiButton } from "@joeattardi/emoji-button";
import * as meetingService from "../../api/meeting";
import * as userService from "../../api/users";
import * as teamService from "../../api/teams";
import "./Content.css";

function Content(props) {
  const { search, changeSearch } = useContext(SearchTheme);
  const { variant, changeVariant } = useContext(TableTheme);
  const { aselect, changeAselect } = useContext(ActiveDropdown);
  const { tselect, changeTselect } = useContext(TimeDropdown);

  const [roomTitle, setRoomTitle] = useState("");
  const [roomIcon, setRoomIcon] = useState("");
  const [roomModalShow, setRoomModalShow] = useState(false);

  const { invitePeopleShow } = useContext(InviteContext);
  const { addMeeting, showAddMeeting } = useContext(AddMeetingContext);

  const {
    showTable,
    showNotificationDelete,
    page,
    showInviteButton,
    showcreatemeeting,
    showcreateroom,
    showcreatefolder,
  } = props;
  const history = useHistory();
  const prevPath = usePrevious(history.location.pathname);

  useEffect(() => {
    if (history.location.pathname !== prevPath) {
      changeSearch("");
      changeAselect("");
      changeTselect("");
    }
  }, [
    history.location.pathname,
    prevPath,
    changeSearch,
    changeAselect,
    changeTselect,
  ]);

  const [emojiTitle, setEmojiTitle] = useState(null);
  const [emojiChoose, setEmojiChoose] = useState(null);
  useEffect(() => {
    if (roomModalShow) {
      setTimeout(() => {
        console.log("setup emoji!!!");
        const titleEmoji = new EmojiButton({
          // position: {
          //   top: `65px`,
          //   left: `0px`,
          // },
          position: "bottom-start",
          strategy: "absolute",
          showVariants: true,
          emojiSize: "16px",
          showPreview: false,
        });
        const trigger = document.querySelector("#title-trigger");

        titleEmoji.on("emoji", (selection) => {
          setEmojiChoose(selection.emoji);
          setEmojiTitle(selection.emoji);
          setRoomIcon(selection.emoji);
        });

        trigger.addEventListener("click", () => {
          titleEmoji.togglePicker(trigger);
        });
      });
    }
  }, [roomModalShow]);

  const eventClick = (e) => {
    e.preventDefault();
  };

  function usePrevious(value) {
    const ref = useRef();
    useEffect(() => {
      ref.current = value;
    });
    return ref.current;
  }

  function addRoom() {
    let userId = userService.getUserInfo().id;
    let teamId = teamService.getCurrentTeam().id;
    let room = {
      title: roomTitle,
      icon: roomIcon,
      createdBy: userId,
      teamId: teamId,
    };
    meetingService
      .addRoom(room)
      .then((res) => res.json())
      .then((data) => {
        console.log("room added: ", data);
        window.dispatchEvent(
          new CustomEvent("new-room", {
            detail: data,
          })
        );
        setRoomModalShow(false);
      })
      .catch((err) => {
        console.log("room error: ", err);
      });
  }

  const activeSelect = ["All", "Active", "In the meeting", "Offline"];
  const localTime = ["All", "Morning", "Afternoon", "Evening", "Night"];
  const appSelect = ["Development", "Document", "Sales", "Design"];

  return (
    <section className="page">
      <Header />
      <div className="page-body">
        <div className="page-sticky">
          <div className="page-head">
            <div className="page-content">
              <h1 className="page-content__title">{page}</h1>
              <div className="page-content__aside">
                <div className="page-content__search">
                  <input
                    id="searchInput"
                    type="text"
                    placeholder="Search"
                    value={search}
                    onChange={(e) => changeSearch(e.target.value)}
                  />
                  <button>
                    <svg className="icon icon-search">
                      <use href={icons + "#icon-search"}></use>
                    </svg>
                  </button>
                </div>
                {showInviteButton && (
                  <div className="page-filter__invite">
                    <Invite />
                  </div>
                )}
                {showcreateroom && (
                  <div className="page-filter__create page-filter__select">
                    <button onClick={() => setRoomModalShow(true)}>
                      <svg className="icon icon-plus-circle ">
                        <use href={icons + "#icon-plus-circle"}></use>
                      </svg>
                      <span>Create Room</span>
                    </button>
                  </div>
                )}

                {showcreatemeeting && (
                  <div className="page-filter__create page-filter__select">
                    <button onClick={() => showAddMeeting(true)}>
                      <svg className="icon icon-plus-circle ">
                        <use href={icons + "#icon-plus-circle"}></use>
                      </svg>
                      <span>Create Meeting</span>
                    </button>
                  </div>
                )}

                {showcreatefolder && (
                  <div className="page-filter__create page-filter__select">
                    <button onClick={() => setRoomModalShow(true)}>
                      <svg className="icon icon-plus-circle ">
                        <use href={icons + "#icon-plus-circle"}></use>
                      </svg>
                      <span>Create Folder</span>
                    </button>
                  </div>
                )}
              </div>
            </div>
          </div>
          <div className="page-tools">
            <div className="page-tools__left">
              <div className="page-filter">
                <div className="page-filter__body">
                  <div className="page-filter__select">
                    <Dropdown
                      values={activeSelect}
                      onValueChanged={(v) => changeAselect(v)}
                    />
                  </div>
                  <div className="page-filter__select">
                    <Dropdown
                      values={localTime}
                      onValueChanged={(v) => changeTselect(v)}
                    />
                  </div>

                  {/*<div className="page-filter__select">
                    <Dropdowns values={appSelect} />
                  </div>*/}
                </div>
              </div>
            </div>
            <div className="page-tools__right">
              {showTable && (
                <div className="page-choose">
                  <ul className="page-choose__list">
                    <li
                      className={`page-choose__item ${
                        variant === "apps" ? "active" : ""
                      }`}
                      onClick={() => changeVariant("apps")}
                    >
                      <svg className="icon icon-view-grid-outline">
                        <use href={icons + "#icon-view-grid-outline"}></use>
                      </svg>
                    </li>
                    <li
                      className={`page-choose__item ${
                        variant === "list" ? "active" : ""
                      }`}
                      onClick={() => changeVariant("list")}
                    >
                      <svg className="icon icon-format-list-bulleted">
                        <use href={icons + "#icon-format-list-bulleted"}></use>
                      </svg>
                    </li>
                  </ul>
                </div>
              )}
              {showNotificationDelete && (
                <button className="page-tools__delete"> Delete All</button>
              )}
            </div>
          </div>
        </div>
        <div className="page-inner">{props.children}</div>
      </div>
      {roomModalShow && (
        <div className="rooms-modal">
          <div className="rooms-modal__body">
            <div className="rooms-modal__content">
              <button
                className="rooms-modal__close"
                onClick={() => setRoomModalShow(false)}
              >
                <svg className="icon icon-cancel">
                  <use href={icons + "#icon-cancel"}></use>
                </svg>
              </button>
              <div id="title-emoji"></div>
              <div className="rooms-modal__head">
                {/* <h2>Create Room</h2> */}
                <button
                  id="title-trigger"
                  type="button"
                  className="crmeeting-form__emoji"
                >
                  {emojiChoose ? (
                    emojiChoose
                  ) : (
                    <svg className="icon icon-emoji">
                      <use href={icons + "#icon-emoji"}></use>
                    </svg>
                  )}
                  <span>Add icon</span>
                </button>
              </div>
              <div className="crmeeting-form__group">
                {/* <label>Title</label> */}
                <div id="emoji-content" className="crmeeting-form__title">
                  <input
                    type="text"
                    placeholder="Add Title"
                    onChange={(e) => setRoomTitle(e.target.value)}
                  />
                </div>
              </div>
              <div className="crmeeting-form__submit">
                <button onClick={(e) => addRoom()}>Save</button>
              </div>
            </div>
          </div>
        </div>
      )}
      {invitePeopleShow.show && <InvitePeople />}
      {addMeeting.show && <AddMeeting />}
    </section>
  );
}

export default Content;
