import * as requests from './requests';
import {config} from './../config';

let files = [];

export function saveFile(file){
	if(!files.find(f => f.id == file.id)){
		files.push(file);
	}
}

export function getFileUrl(id){
	return files.find(f => f.id == id) ? files.find(f => f.id == id).url : '';
}

export function getFile(id)
{	
	return requests.getAuth(config.services.file + 'file/' + id + '/url');
}

export function uploadFile(file)
{
	const formData = new FormData();
    formData.append('file', file);
    formData.append('data', '');
	return requests.postAuthFile(config.services.file + `file?isPrivate=false&name=${file.name}`, formData);
}
