import React, { useState, useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";
import "../Auth.css";
import logo from "../../../assets/image/logo/wteamlogo.jpeg";
import * as userService from '../../../api/users';
import * as requests from '../../../api/requests';
import * as messagingService from '../../../api/messaging';

const ElectronGoogleOAuth2 = window.require('@getstation/electron-google-oauth2').default;


function SignIn() {
  const ref = useRef(null);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [validateFields, setValidateFields] = useState(false);
  const [errorText, setErrorText] = useState("");
  const [startSocial, setStartSocial] = useState(false);
  const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  let history = useHistory();

  useEffect(() => {
    setTimeout(() => {
      setStartSocial(true);
    }, 1500);
  });

  function signIn(){
    setValidateFields(true);
    setErrorText("");
    
    console.log(email, password);

    if(email && email.trim().length && re.test(String(email).toLowerCase()) && password && password.trim().length){
      userService.signIn({email, password}).then(res => res.json())
                                        .then(data => {
                                          console.log('user data: ', data);
                                          
                                          if(data && data.token){
                                            requests.setToken(data.token);
                                            userService.setUserInfo(data);

                                            if(data.preferences){
                                              userService.setUserPreferences(data.preferences);
                                            } else {
                                              userService.setUserPreferences({});
                                            }
                                            
                                            messagingService.getUnreadMessagesUsers().then(res => res.json())
                                            .then(msgUsers => {
                                              console.log('message data: ', msgUsers);
                                              messagingService.setLastMessageUsers(msgUsers);

                                              history.push('/dashboard');

                                              setTimeout(() => {
                                                messagingService.connect();
                                              }, 500);
                                            }).catch(err => {
                                              console.log('user error: ', err);
                                            });

                                          } else if(data && data.message){
                                            if(data.message == "invalidCredentials"){
                                              setErrorText("Your email and password don't match");
                                            } else if(data.message == "userNotFound"){
                                              setErrorText("This email hasn't been registered in our platform");
                                            } else {
                                              setErrorText("Something going wron..");
                                            }
                                          }
                                        })
                                        .catch(err => {
                                          console.log('user err: ', err);
                                          if(err && err.message == "invalidCredentials"){
                                            setErrorText("Your email and password don't match");
                                          } else if(err && err.message == "userNotFound"){
                                            setErrorText("This email hasn't been registered in our platform");
                                          } else {
                                            setErrorText("Something going wrong");
                                          }
                                        });
    }
    
  }

  function handleKeyPress(event){
    if(event.key === 'Enter'){
      signIn();
    }
  }

  function signInWithGoogle(){
    const myApiOauth = new ElectronGoogleOAuth2(
    '931715072311-jvrgiupblfe84oald46d23mhs1uhki6v.apps.googleusercontent.com',
    'q4ZBB9vdIoTQ62A6JoklHJ5e',
    ['https://www.googleapis.com/auth/userinfo.profile', 'https://www.googleapis.com/auth/userinfo.email']
    );
 
    myApiOauth.openAuthWindowAndGetTokens()
    .then((token) => {
      // use your token.access_token
      console.log("response token: ", token);

      if(token && token.id_token){
        let socialUserLogin = {};
            socialUserLogin.socialType = 'google';
            socialUserLogin.socialToken = token.id_token;

            userService.signInWithSocial(socialUserLogin).then(res => res.json())
                                        .then(data => {
                                          console.log('user data: ', data);
                                          
                                          if(data && data.token){
                                            requests.setToken(data.token);
                                            userService.setUserInfo(data);

                                            if(data.preferences){
                                              userService.setUserPreferences(data.preferences);
                                            } else {
                                              userService.setUserPreferences({});
                                            }
                                            
                                            messagingService.getUnreadMessagesUsers().then(res => res.json())
                                            .then(msgUsers => {
                                              console.log('message data: ', msgUsers);
                                              messagingService.setLastMessageUsers(msgUsers);

                                              history.push('/dashboard');

                                              setTimeout(() => {
                                                messagingService.connect();
                                              }, 500);
                                            }).catch(err => {
                                              console.log('user error: ', err);
                                            });

                                          } else if(data && data.message){
                                            if(data.message == "invalidCredentials"){
                                              setErrorText("Your email and password don't match");
                                            } else if(data.message == "userNotFound"){
                                              setErrorText("This email hasn't been registered in our platform");
                                            } else {
                                              setErrorText("Something going wron..");
                                            }
                                          }
                                        })
                                        .catch(err => {
                                          console.log('user err: ', err);
                                          if(err && err.message == "invalidCredentials"){
                                            setErrorText("Your email and password don't match");
                                          } else if(err && err.message == "userNotFound"){
                                            setErrorText("This email hasn't been registered in our platform");
                                          } else {
                                            setErrorText("Something going wrong");
                                          }
                                        });
      }
    });
  }

  return (
    <div className="auth">
      <div className="auth-body">
        <div className="auth-content">
          <div className="auth-inner">
            <div className="auth-logo">
              <img src={logo} alt="Logo" />
            </div>
            <div className="auth-title">
              <h1>Sign in to your account</h1>
            </div>
            <div className="auth-form">
              <div className="auth-form__content">
                <div className="auth-form__group">
                <button onClick={(e) => signInWithGoogle()} className="social">Countinue with Google</button>
                </div>
                <div className="auth-or">
                  <div className="auth-or__content">OR</div>
                </div>
                <div className={`auth-form__group ${validateFields && (!email.trim().length || !re.test(email)) ? 'auth-form__group__error' : ''}`}>
                  <input type="text" placeholder="name@work-email.com" value={email} onChange={(e) => setEmail(e.target.value)} onKeyPress={(e) => handleKeyPress(e)} />
                </div>
                <div className={`auth-form__group ${validateFields && !password.trim().length ? 'auth-form__group__error' : ''}`}>
                  <input type="password" placeholder="*******************" value={password} onChange={(e) => setPassword(e.target.value)} onKeyPress={(e) => handleKeyPress(e)}  />
                </div>
                <div className="auth-form__group">
                  <button className="submit" onClick={(e) => signIn()}>Sign in</button>
                </div>
                <div className="auth-form__group">
                  <span>{errorText}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SignIn;
