import * as requests from './requests';
import {config} from './../config';

let team = '', currentTeam = {}, teams = [], member = {}, members = [];

export function clearTeamService(){
	team = '';
	currentTeam = {};
	teams = [];
	member = {};
	members = {};
}

export function getCurrentTeam()
{
	return currentTeam;
}

export function setCurrentTeam(t)
{
	team = t;
	currentTeam = teams.find(tm => tm.title == team);
	window.dispatchEvent(new CustomEvent("team-selected", {
    	detail: {team: team}
    }));
    window.dispatchEvent(new CustomEvent("team-selected-invitation", {
    	detail: {team: team}
    }));
}

export function getTeams()
{
	return teams;
}

export function setTeams(t)
{
	teams = [];
	teams.push(...t);
}

export function getCurrentMemberProfile()
{
	return member;
}

export function setCurrentMemberProfile(m)
{
	member = m;
}

export function getMembers()
{
	return members;
}

export function setMembers(m)
{
	members = [];
	members.push(...m);
}

export function getUserTeams()
{
	return requests.getAuth(config.services.team + 'member/teams');
}

export function getUserTeamIds()
{
	return requests.getAuth(config.services.team + 'member/team-ids');
}

export function getTeam(teamId)
{
	return requests.getAuth(config.services.team + 'team/' + teamId);
}

export function createTeam(teamTitle, pref)
{
	return requests.postAuth(config.services.team + 'team/' + teamTitle, {preferences: pref});
}

export function updateTeam(team)
{
	return requests.putAuth(config.services.team + 'team' , team);
}

export function getTeamMembers(teamId)
{
	return requests.getAuth(config.services.team + 'team/' + teamId + '/users');
}

export function getMemberProfiles(teamId)
{
	return requests.getAuth(config.services.team + 'member/' + teamId + '/profiles');
}

export function getMemberProfile(teamId)
{
	return requests.getAuth(config.services.team + 'member/' + teamId + '/by-team-id');
}

export function createMemberProfile(teamId, user)
{
	return requests.postAuth(config.services.team + 'member/profile/'+teamId+'/undefined', user);
}

export function updateMemberProfile(member)
{
	let m = {...member};
	m.id = m._id;
	delete m._id;
	return requests.putAuth(config.services.team + 'member/profile', m);
}

export function deleteMemberProfile(memberId)
{
	return requests.deleteReqAuth(config.services.team + 'member/' + memberId);
}

export function inviteMember(email)
{
	let data = {
		email: email,
        roleProfile: "teamMember",
        teamId: currentTeam.id
	};
	return requests.postAuth(config.services.team + 'invitation', data);
}
