import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import person from "../../assets/image/other/person@2x.png";
import google from "../../assets/image/other/google.png";
import slack from "../../assets/image/other/slack.png";
import chrome from "../../assets/image/other/chrome.png";
import spotify from "../../assets/image/other/spotify.png";
import icons from "../../assets/image/icons.svg";
import * as userService from "../../api/users";
import * as fileService from "../../api/files";
import "./StAside.css";

function StAside() {

  const [imageurl, setImageurl] = useState('');
  const [name, setName] = useState('');
  const [surname, setSurname] = useState('');

  useEffect(() => {
    let user = userService.getUserInfo();

    setName(user.name.first);
    setSurname(user.name.last);

    if(user.avatar){
      fileService.getFile(user.avatar).then((res) => res.json())
      .then((data) => {
        setImageurl(data);
      }).catch((err) => {
        console.log("members error: ", err);
      });
    }
    
  }, []);

  return (
    <aside className="staside">
      <div className="staside-body">
        <div className="staside-body__top"></div>
        <div className="staside-body__content">
          <nav className="staside-navbar">
            <Link to="/settings/account">
              <div className="staside-navbar__icon">
                { imageurl && (
                  <img src={imageurl} alt="Person" />
                )}
                { !imageurl && (
                  <p>{name.charAt(0)}{surname.charAt(0)}</p>
                )}
              </div>
              <span>Account</span>
            </Link>
            <Link to="/settings/preferences">
              <div className="staside-navbar__icon">
                <svg className="icon icon-preferences">
                  <use href={icons + "#icon-preferences"}></use>
                </svg>
              </div>
              <span>Preferences</span>
            </Link>
            <Link to="/settings/sharing">
              <div className="staside-navbar__icon">
                <svg className="icon icon-apps">
                  <use href={icons + "#icon-apps"}></use>
                </svg>
              </div>
              <span>Shared Apps</span>
            </Link>
          </nav>
        </div>
        <div className="staside-body__content">
          <h1>Integrations</h1>
          <nav className="staside-navbar">
            <Link to="/settings/googlecalendar">
              <div className="staside-navbar__icon">
                <img src={google} alt="Person" />
              </div>
              <span>Google Calendar</span>
            </Link>
            <Link to="/settings/slack">
              <div className="staside-navbar__icon">
                <img src={slack} alt="Person" />
              </div>
              <span>Slack</span>
              <div className="staside-coming">Coming Soon</div>
            </Link>
            <Link to="/settings/chrome">
              <div className="staside-navbar__icon">
                <img src={chrome} alt="Person" />
              </div>
              <span>Chrome</span>
            </Link>
            <Link to="/settings/spotify">
              <div className="staside-navbar__icon">
                <img src={spotify} alt="Person" />
              </div>
              <span>Spotify</span>
              <div className="staside-coming">Coming Soon</div>
            </Link>
          </nav>
        </div>
        <div className="staside-body__content">
          <h1>Teams</h1>
          <nav className="staside-navbar">
            <Link to="/settings/team">
              <div className="staside-navbar__icon">
                <p>w</p>
              </div>
              <span>wasco.team</span>
            </Link>
          </nav>
          <div className="staside-add">
            <Link to="/settings/createteam">+ Add new team</Link>
          </div>
        </div>
      </div>
    </aside>
  );
}

export default StAside;
