import React, { useState, useEffect, useRef } from "react";
import icons from "../../../assets/image/icons.svg";
import "./Dropdown.css";

function Dropdown({ values, onValueChanged, svalue }) {
  const [open, setOpen] = useState(false);
  const [value, setValue] = useState('');
  const ref = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setOpen(false);
      }
    }
    document.addEventListener("click", handleClickOutside);
    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, [ref, value]);

  useEffect(() => {
    if (!value && svalue) {
      setValue(svalue);
    } else if (!value) {
      setValue(values[0]);
    }
  }, [values]);

  useEffect(() => {
    setValue(svalue);
  }, [svalue]);

  function onSelected(v) {
    onValueChanged(v);
  }

  return (
    <div className="dropdown" ref={ref}>
      <div className="dropdown-body">
        <button
          className={`dropdown-toggle ${open ? "open" : ""}`}
          onClick={() => setOpen(!open)}
        >
          <span>{value}</span>
          <div className="dropdown-toggle__icons">
            <svg className="icon icon-down-abgle">
              <use href={icons + "#icon-down-abgle"}></use>
            </svg>
          </div>
        </button>
        <div className={`dropdown-select ${open ? "open" : ""}`}>
          <ul className="dropdown-list">
            {values.map((v, i) => (
              <li
                key={i}
                className="dropdown-item"
                onClick={() => {
                  setOpen(!open);
                  setValue(v);
                  onSelected(v);
                }}
              >
                {v}
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Dropdown;
