import React, { useContext, useEffect, useState, useMemo } from "react";
import { useHistory } from "react-router-dom";
import Layout from "../../hoc/Layout/Layout";
import Teammates from "./Teammates";
import Content from "../../hoc/Content/Content";
import { SearchTheme } from "../../context/SearchContext";
import { ActiveDropdown } from "../../context/ActiveDropdownContext";
import { TimeDropdown } from "../../context/TimeDropdownContext";
import { TableTheme } from "../../context/TableVariant";
import * as requests from "../../api/requests";
import * as teamService from "../../api/teams";
import * as userService from "../../api/users";
import * as messagingService from "../../api/messaging";
import Feedback from "../../components/Feedback/Feedback";

function Home() {
  const [results, setResults] = useState([]);
  const [teammates, setTeammates] = useState([]);
  const { search } = useContext(SearchTheme);
  const { aselect } = useContext(ActiveDropdown);
  const { tselect } = useContext(TimeDropdown);
  const { variant } = useContext(TableTheme);
  const [activeUsers, setActiveUsers] = useState([]);
  const [msgUsers, setMsgUsers] = useState([]);
  const [hideInvite, setHideInvite] = useState(false);
  const [dataLoaded, setDataLoaded] = useState(false);

  let history = useHistory(),
    eventListener,
    teamEventListener,
    messageUsersEventListener;

  setTimeout(() => {
    let oldActiveUsers = JSON.stringify(activeUsers),
      newActiveUsers = JSON.stringify(messagingService.getActiveUsers());
    if (oldActiveUsers != newActiveUsers) {
      setActiveUsers([...messagingService.getActiveUsers()]);
      //console.log('activeUsers: ', activeUsers);
    }
  }, 1500);

  useEffect(() => {
    if (eventListener == undefined || eventListener == null) {
      eventListener = window.addEventListener("new-notification", function (
        event
      ) {
        console.log("new notification: ", event.detail);
        let data = event.detail;

        switch (parseInt(data.type)) {
          case 1:
            //console.log("active users: ", data.data);
            if(data.data.users){
              let newActiveUsers = [];
              newActiveUsers.push(...data.data.users);

              let oldActiveUsers = activeUsers;
              if (
                JSON.stringify(oldActiveUsers) != JSON.stringify(newActiveUsers)
              ) {
                setActiveUsers([...newActiveUsers]);
                let allResults = [...results];

                if (allResults.length) setResults([...allResults]);
              }
            }
            
            break;
        }
      });
    }

    if (teamEventListener == undefined || teamEventListener == null) {
      teamEventListener = window.addEventListener("team-selected", function (
        event
      ) {
        let allTeams = teamService.getTeams();
        let selectedTeamId = allTeams.find((t) => t.title == event.detail.team)
          .id;
        teamService
          .getTeamMembers(selectedTeamId)
          .then((res) => res.json())
          .then((data) => {
            console.log("team members: ", data);
            teamService.setMembers(data);
            let user = userService.getUserInfo();
            if (Array.isArray(data)) {
              data = data.filter((d) => d.id != user.id);
              setTeammates([...data]);
              setResults([...data]);
            }
          })
          .catch((err) => {
            console.log("members error: ", err);
          });

        let cteam = teamService.getCurrentTeam();
        let userInfo = userService.getUserInfo();

        if (
          cteam &&
          cteam.preferences &&
          cteam.preferences.onlyAdminInvite &&
          userInfo.memberProfile &&
          userInfo.memberProfile.role != "teamSuperAdmin" &&
          userInfo.memberProfile.role != "teamAdmin"
        ) {
          setHideInvite(true);
        } else {
          setHideInvite(false);
        }
      });
    }

    if (
      messageUsersEventListener == undefined ||
      messageUsersEventListener == null
    ) {
      messageUsersEventListener = window.addEventListener(
        "last-message-users",
        function () {
          let msgusers = messagingService.getLastMessageUsers();
          console.log("messages: ", msgusers);
          setMsgUsers([...msgusers]);
          let allResults = [...results];

          if (allResults.length) setResults([...allResults]);
        }
      );
    }

    if(teamService.getTeams() && teamService.getCurrentTeam() && teamService.getCurrentTeam().title){
        let allTeams = teamService.getTeams();
        let selectedTeamId = allTeams.find((t) => t.title == teamService.getCurrentTeam().title)
          .id;
        teamService
          .getTeamMembers(selectedTeamId)
          .then((res) => res.json())
          .then((data) => {
            console.log("team members: ", data);
            setDataLoaded(true);
            teamService.setMembers(data);
            let user = userService.getUserInfo();
            if (Array.isArray(data)) {
              data = data.filter((d) => d.id != user.id);
              setTeammates([...data]);
              setResults([...data]);
            }
          })
          .catch((err) => {
            console.log("members error: ", err);
          });
    }

    let msgusers = messagingService.getLastMessageUsers();
    setMsgUsers([...msgusers]);

    setTimeout(() => {
      console.log('all teams: ', results)
    }, 2500);
  }, []);

  useMemo(() => {
    setResults([
      ...teammates.filter((team) => {
        return (
          team.name.first
            .toLocaleLowerCase()
            .indexOf(search.toLocaleLowerCase()) !== -1
        );
      }),
    ]);
  }, [search]);

  useMemo(() => {
    console.log("a select!!!: ", aselect);
    switch (aselect) {
      case "All":
        // statements_1
        setResults([
          ...teammates.filter((team) => {
            return (
              team.name.first
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase()) !== -1
            );
          }),
        ]);
        break;
      case "Active":
        // statements_1
        setResults([
          ...teammates.filter((team) => {
            return (
              activeUsers &&
              activeUsers.map((user) => user.id).includes(team.id) &&
              team.name.first
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase()) !== -1
            );
          }),
        ]);
        break;
      case "In the meeting":
        // statements_1
        setResults([
          ...teammates.filter((team) => {
            return (
              activeUsers &&
              activeUsers.map((user) => user.id).includes(team.id) &&
              activeUsers.find((user) => user.id == team.id).inmeeting &&
              team.name.first
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase()) !== -1
            );
          }),
        ]);
        break;
      case "Offline":
        // statements_1
        setResults([
          ...teammates.filter((team) => {
            return (
              (!activeUsers ||
                !activeUsers.map((user) => user.id).includes(team.id)) &&
              team.name.first
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase()) !== -1
            );
          }),
        ]);
        break;
    }
  }, [aselect]);

  useMemo(() => {
    console.log("a select!!!: ", aselect);
    switch (tselect) {
      case "All":
        // statements_1
        setResults([
          ...teammates.filter((team) => {
            return (
              team.name.first
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase()) !== -1
            );
          }),
        ]);
        break;
      case "Morning":
        // statements_1
        setResults([
          ...teammates.filter((team) => {
            return (
              getHour(team.timezoneOffset) >= 6 &&
              getHour(team.timezoneOffset) <= 12 &&
              team.name.first
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase()) !== -1
            );
          }),
        ]);
        break;
      case "Afternoon":
        // statements_1
        setResults([
          ...teammates.filter((team) => {
            return (
              getHour(team.timezoneOffset) >= 13 &&
              getHour(team.timezoneOffset) <= 17 &&
              team.name.first
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase()) !== -1
            );
          }),
        ]);
        break;
      case "Evening":
        // statements_1
        setResults([
          ...teammates.filter((team) => {
            return (
              getHour(team.timezoneOffset) >= 18 &&
              getHour(team.timezoneOffset) <= 22 &&
              team.name.first
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase()) !== -1
            );
          }),
        ]);
        break;
      case "Night":
        // statements_1
        setResults([
          ...teammates.filter((team) => {
            return (
              getHour(team.timezoneOffset) >= 23 &&
              getHour(team.timezoneOffset) <= 5 &&
              team.name.first
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase()) !== -1
            );
          }),
        ]);
        break;
    }
  }, [tselect]);

  function getHour(offset) {
    let d = new Date();
    let n = d.getTimezoneOffset();

    let diff = Math.abs(n) - Math.abs(offset);

    let newDate = new Date();
    newDate.setMinutes(d.getMinutes() + diff);
    return newDate.getHours();
  }

  function getTime(offset) {
    let d = new Date();
    let n = d.getTimezoneOffset();

    let diff = Math.abs(n) - Math.abs(offset);

    let newDate = new Date();
    newDate.setMinutes(d.getMinutes() + diff);
    return (
      (newDate.getHours() < 10
        ? "0" + newDate.getHours()
        : newDate.getHours()) +
      ":" +
      (newDate.getMinutes() < 10
        ? "0" + newDate.getMinutes()
        : newDate.getMinutes())
    );
  }

  return (
    <Layout>
      <Content page="Teammates" showTable showInviteButton={!hideInvite}>
        <div className="page-teammates">
          <div
            className={`page-teammates__body ${
              variant === "list" ? "list" : ""
            }`}
          >
            {results.map((team, i) => (
              <Teammates
                key={i}
                activeUsers={activeUsers}
                msgUsers={msgUsers}
                name={team.name.first}
                time={getTime(team.timezoneOffset)}
                lastname={team.name.last}
                id={team.id}
                avatar={team.avatar}
                shareApp={team.memberProfile.preferences ? team.memberProfile.preferences.shareApp : true}
                shareAppUrl={team.memberProfile.preferences ? team.memberProfile.preferences.shareAppUrl : true}
                list={variant === "list" ? true : false}
              />
            ))}

            {dataLoaded && results.length == 0 && (
            <div className="no-scenario">
                <div className="no-scenario-img no-scenario-img-dash">
                </div>
              </div>
          )}
            </div>
        </div>
      </Content>
    </Layout>
  );
}

export default Home;
