import React, { useState, useEffect } from "react";
import StLayout from "../StLayout/StLayout";
import "../Settings.css";
import SharingTeammate from "./SharingTeammate";
import icons from "../../../assets/image/icons.svg";
import appIcons from "../../../assets/image/appicons.svg"
import { Link } from "react-router-dom";
import doc from "../../../assets/image/other/doc.png";
import callScreen from "../../../assets/image/other/teammate card.svg";
import person from "../../../assets//image/other/person2.png"
import Layout from "../../../hoc/Layout/Layout";
import * as userService from "../../../api/users";

function Sharing() {
  const [showDoc, setShowDoc] = useState(null);
  const [checked, setChecked] = useState(null);
  const [showDoc1, setShowDoc1] = useState(null);
  const [checked1, setChecked1] = useState(null);

  useEffect(() => {
    let preferences = userService.getUserPreferences();

    if (preferences && preferences.shareActiveApp) {
      setChecked1(true);
      setShowDoc1(true);
    } else {
      setChecked1(false);
      setShowDoc1(false);
    }

    if (preferences && preferences.shareActiveAppUrl) {
      setChecked(true);
      setShowDoc(true);
    } else {
      setChecked(false);
      setShowDoc(false);
    }

  }, []);

  useEffect(() => {
    if (checked != undefined && checked != null && checked1 != undefined && checked1 != null) {
      let preferences = {
        shareActiveApp: checked1,
        shareActiveAppUrl: checked
      };
      console.log("prefs: ", preferences, checked);
      userService.updatePreferences(preferences)
        .then(res => res.json())
        .then(data => {
          userService.setUserPreferences(data);
        });
    }
  }, [checked, checked1]);

  function updatePreferences(type) {
    console.log("update preferences: ", type);
    switch (type) {
      case 'app':
        if (checked && checked1) {
          setChecked(!checked);
          setShowDoc(!showDoc);
        }

        setChecked1(!checked1);
        setShowDoc1(!showDoc1);
        break;
      case 'appUrl':
        if (!checked && !checked1) {
          setChecked1(!checked1);
          setShowDoc1(!showDoc1);
        }

        setChecked(!checked);
        setShowDoc(!showDoc);
        break;
    }

  }

  return (
    <Layout>
      <StLayout page="Work App Sharing">
        <div className="sharing">
          <div className="sharing-body">
            <div className="sharing-body__head">
              <p>
                In office, you can tell who’s deep in thought or free to talk.
                When you’re remote sharing your current work app provide similar
                social cues, building connection with your team
              </p>
            </div>
            <div className="sharing-content">
              <div className="sharing-content__head">
                <h2>Let my teammates know what I’m up to</h2>
                <p>
                  By sharing your current work app, your team can understand
                  whether you’re free or busy, and get a feel for what mode
                  you’re in.
                </p>
              </div>
              <div className="preferences-choose sharing-choose">
                <div className="preferences-choose__head">
                  <div className="preferences-choose__info">
                    <span>
                      Share currently active work app with my teammates
                    </span>
                  </div>
                  <label className="preferences-checkbox">
                    <input type="checkbox" hidden checked={checked1}
                      onChange={() => {
                        updatePreferences('app');
                      }} />
                    <span></span>
                  </label>
                </div>
              </div>
              <div className="sharing-bottom">
                <div className="sharing-bottom__content">
                  <SharingTeammate name="Valeh" meeting={false} online showDocs={showDoc1} />
                </div>
              </div>
            </div>
            <div className="sharing-content">
              <div className="sharing-content__head">
                <h2>Get on the same page</h2>
                <p>
                  By sharing your current work app, during a call your teammates
                  can join you in one click (in a Google doc for example)
                </p>
              </div>
              <div className="preferences-choose sharing-choose">
                <div className="preferences-choose__head">
                  <div className="preferences-choose__info">
                    <span>
                      While in a call let teammates join my work app URL
                    </span>
                  </div>
                  <label className="preferences-checkbox">
                    <input
                      type="checkbox"
                      hidden
                      checked={checked}
                      onChange={() => {
                        updatePreferences('appUrl');
                      }}
                    />
                    <span></span>
                  </label>
                </div>
              </div>
              <div className="sharing-bottom">
                <div className="sharing-bottom__content">
                  <div className="teammates-body__grid">
                    <div className="teammates-about">

                      <div className="teammates-about__image">
                        <img src={person} alt="Person" />
                        <div className="teammates-about__image--active">
                          <svg
                            className={`icon icon-circul`}
                          >
                            <use href={icons + "#icon-circul"}></use>
                          </svg>
                        </div>
                      </div>
                      <div className="teammates-about__data">
                        <div className="teammates-about__name">
                          <h2>Tural Z.</h2>

                        </div>
                        <ul className="teammates-about__list">
                          <li className="teammates-about__item">

                            <span>12:00</span>
                          </li>
                          <li className="teammates-about__item line"><span>|</span></li>
                          <li className="teammates-about__item">
                            <span>In Meeting</span>
                          </li>
                        </ul>



                        {checked ? <div className="teammates-current">
                          <h3>Active app:</h3>
                          <div className="teammates-current__file">
                            <div className="teammates-current__icon">
                              <svg className="icon icon-adobe-xd-1">
                                <use href={appIcons + "#visual-studio-code"}></use>
                              </svg>
                            </div>
                            <div className="teammates-current__name">
                              <Link to="#app"> VS Code | wteam_app dev</Link>
                            </div>
                          </div>
                        </div> :

                          <div className="teammates-current">
                            <h3>Active app:</h3>
                            <div className="teammates-current__file">
                              <div className="teammates-current__name name-padding">
                                <span>No active app</span>
                              </div>
                            </div>
                          </div>}

                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </StLayout>
    </Layout>
  );
}

export default Sharing;
