export default function Teammates({
  activeUsers,
  msgUsers,
  name,
  time,
  lastname,
  list,
  id,
  avatar,
  shareApp,
  shareAppUrl,
}) {
  const [tools, setTools] = useState(false);
  const [chatShow, setChatShow] = useState(false);
  const [avatarurl, setAvatarurl] = useState("");

  const ref = useRef(null);

  useEffect(() => {
    console.log("avatar, avatar-url: ", avatar, avatarurl);
    if (avatar && !avatarurl) {
      let url = filesService.getFileUrl(avatar);
      if (url) {
        setAvatarurl(url);
      } else {
        filesService
          .getFile(avatar)
          .then((res) => res.json())
          .then((data) => {
            filesService.saveFile({ id: avatar, url: data });
            setAvatarurl(data);
            console.log("data: ", data);
          });
      }
    }
  }, []);

  function startVideoCall(id) {
    console.log("user id: ", id);
    let user = activeUsers.find((user) => user.id == id);
    if (!callButtonClicked && user && !user.inmeeting && user.appStatus != "Focus mode") {
      callButtonClicked = true;
      let user = userService.getUserInfo();
      user.uType = user.type;
      let noti = {
        code: "user.video-call",
        actingUser: user,
        otherUser: { id: id },
        involved: [id],
      };

      messagingService
        .addNotification(noti)
        .then((res) => res.json())
        .then((data) => {
          let fromId = user.id;

          let path = electron.getGlobal("sharedPath");
          let filePath = electron.getGlobal("filePath");

          const BrowserWindow = electron.BrowserWindow;
          let callWindow = new BrowserWindow({
            width: 300,
            height: 533,
            title: "Wteam call",
            frame: false,
            transparent: true,
            resizable: false,
            fullscreen: false,
            webPreferences: {
              preload: filePath + "../public/preload.js",
              nodeIntegration: true,
              enableRemoteModule: true,
              webSecurity: false,
            },
          });
          callWindow.setFullScreenable(true);
          callWindow.loadURL(
            path +
              "../call/call.html?roomId=" +
              id +
              "&fromId=" +
              fromId +
              "&token=" +
              requests.getToken()
          );

          setTimeout(() => {
            callButtonClicked = false;
          })
        })
        .catch((err) => {
          console.log("members error: ", err);
        });
    }
  }

  function startAudioCall(id) {
    console.log("user id: ", id);
    let user = activeUsers.find((user) => user.id == id);
    if (!callButtonClicked && user && !user.inmeeting && user.appStatus != "Focus mode") {
      callButtonClicked = true;
      let user = userService.getUserInfo();
      user.uType = user.type;
      let noti = {
        code: "user.audio-call",
        actingUser: user,
        otherUser: { id: id },
        involved: [id],
      };

      messagingService
        .addNotification(noti)
        .then((res) => res.json())
        .then((data) => {
          let fromId = user.id;

          let path = electron.getGlobal("sharedPath");
          let filePath = electron.getGlobal("filePath");

          const BrowserWindow = electron.BrowserWindow;
          let callWindow = new BrowserWindow({
            width: 300,
            height: 533,
            title: "Wteam call",
            frame: false,
            transparent: true,
            resizable: false,
            fullscreen: false,
            webPreferences: {
              preload: filePath + "../public/preload.js",
              nodeIntegration: true,
              enableRemoteModule: true,
              webSecurity: false,
            },
          });

          callWindow.setFullScreenable(true);

          callWindow.loadURL(
            path +
              "../call/call.html?onlyAudio=true&roomId=" +
              id +
              "&fromId=" +
              fromId +
              "&token=" +
              requests.getToken()
          );

          setTimeout(() => {
            callButtonClicked = false;
          })
        })
        .catch((err) => {
          console.log("members error: ", err);
        });
    }
  }

  function startChat(id) {
    let fromId = userService.getUserInfo().id;
    let path = electron.getGlobal("sharedPath");
    let filePath = electron.getGlobal("filePath");
    const BrowserWindow = electron.BrowserWindow;
    let chatWindow = new BrowserWindow({
      width: 300,
      height: 500,
      title: "Wteam chat",
      frame: false,
      transparent: true,
      resizable: false,
      fullscreen: false,
      webPreferences: {
        preload: filePath + "../public/preload.js",
        nodeIntegration: true,
        enableRemoteModule: true,
        webSecurity: false,
      },
    });
    chatWindow.loadURL(
      path +
        "../chat/mainc.html?fromId=" +
        fromId +
        "&toId=" +
        id +
        "&token=" +
        requests.getToken()
    );
    messagingService.removeLastMessageUser(id);
  }

  function getUserStatusIcon(id) {
    let user = activeUsers.find((user) => user.id == id);
    return user && user.inmeeting ? "meeting" : user ? "online" : "";
  }

  function getUserStatus(id) {
    let user = activeUsers.find((user) => user.id == id);
    let status = user && user.appStatus ? user.appStatus : undefined;
    return user && user.inmeeting
      ? "meeting"
      : user && status
      ? status
      : user
      ? "online"
      : "";
  }

  function getUserUrl(id) {
    if (shareAppUrl) {
      let activeWindow =
        activeUsers.find((user) => user.id == id) &&
        activeUsers.find((user) => user.id == id).activeWindow
          ? activeUsers.find((user) => user.id == id).activeWindow
          : null;
      let activeTab =
        activeUsers.find((user) => user.id == id) &&
        activeUsers.find((user) => user.id == id).extensionTab
          ? activeUsers.find((user) => user.id == id).extensionTab
          : null;
      let url =
        activeWindow && activeWindow.url
          ? activeWindow.url
          : activeTab && activeTab.url
          ? activeTab.url
          : "";

      return url && url.length ? true : false;
    } else {
      return false;
    }
  }

  function isNewMessage(id) {
    return msgUsers && msgUsers.includes(id) ? true : false;
  }

  function getAppTitle(id) {
    let activeTab =
      activeUsers.find((user) => user.id == id) &&
      activeUsers.find((user) => user.id == id).extensionTab
        ? activeUsers.find((user) => user.id == id).extensionTab
        : null;
    let activeWindow =
      activeUsers.find((user) => user.id == id) &&
      activeUsers.find((user) => user.id == id).activeWindow
        ? activeUsers.find((user) => user.id == id).activeWindow
        : null;

    return activeWindow && activeWindow.title
      ? activeWindow.title
      : activeTab && activeTab.title
      ? activeTab.title
      : "";
  }

  function getUserAppIcon(id) {
    let activeTab =
      activeUsers.find((user) => user.id == id) &&
      activeUsers.find((user) => user.id == id).extensionTab
        ? activeUsers.find((user) => user.id == id).extensionTab
        : null;
    let activeWindow =
      activeUsers.find((user) => user.id == id) &&
      activeUsers.find((user) => user.id == id).activeWindow
        ? activeUsers.find((user) => user.id == id).activeWindow
        : null;

    return activeWindow && activeWindow.icon
      ? appicons + "#" + activeWindow.icon
      : activeTab && activeTab.icon
      ? appicons + "#" + activeTab.icon
      : "";
  }

  function goToDoc(id) {
    let activeTab =
      activeUsers.find((user) => user.id == id) &&
      activeUsers.find((user) => user.id == id).extensionTab
        ? activeUsers.find((user) => user.id == id).extensionTab
        : null;
    let activeWindow =
      activeUsers.find((user) => user.id == id) &&
      activeUsers.find((user) => user.id == id).activeWindow
        ? activeUsers.find((user) => user.id == id).activeWindow
        : null;
    let url =
      activeWindow && activeWindow.url
        ? activeWindow.url
        : activeTab && activeTab.url
        ? activeTab.url
        : "";

    if (url && shareAppUrl) {
      if (shell) {
        shell.openExternal(url);
      } else {
        window.open(url);
      }
    } else {
      let user = activeUsers.find((user) => user.id == id);
      if (user && !user.inmeeting && user.appStatus != "Focus mode") {
        let user = userService.getUserInfo();
        user.uType = user.type;
        let noti = {
          code: "user.request-share",
          actingUser: user,
          otherUser: { id: id },
          involved: [id],
        };

        messagingService.addNotification(noti);
      }
    }
  }

  function nameSubstring(name) {
    let str = name.substring(0, 30) + "...";
    return str;
  }

  return (
    <div className={`teammates ${list ? "list" : ""}`} ref={ref}>
      <div className={`teammates-body ${list ? "list" : ""}`}>
        <div className="teammates-body__grid">
          <div className="teammates-about" onClick={(e) => setTools(!tools)}>
            {avatarurl && (
              <div className="teammates-about__image">
                <img src={avatarurl} alt="Person" />
                <div className="teammates-about__image--active">
                  <svg className={`icon icon-circul ${getUserStatusIcon(id)}`}>
                    <use href={icons + "#icon-circul"}></use>
                  </svg>
                </div>
              </div>
            )}
            {!avatarurl && (
              <div className="teammates-about__image teammates-about__image--name">
                <p>
                  {name.charAt(0)}
                  {lastname.charAt(0)}
                </p>
                <div className="teammates-about__image--active">
                  <svg className={`icon icon-circul ${getUserStatusIcon(id)}`}>
                    <use href={icons + "#icon-circul"}></use>
                  </svg>
                </div>
              </div>
            )}
            <div className="teammates-about__data">
              <div className="teammates-about__name">
                <h2>
                  {name} {lastname}
                </h2>

                {/*{getUserAppIcon(id) && (
                  <a
                    href="javascript:void(0)"
                  >
                    <svg className="icon">
                      <use href={getUserAppIcon(id)}></use>
                    </svg>
                  </a>
                )}*/}
              </div>
              <ul className="teammates-about__list">
                <li className="teammates-about__item">
                  <span>{time}</span>
                </li>
                <li className="teammates-about__item line">
                  <span>|</span>
                </li>
                <li className="teammates-about__item">
                  { getUserStatus(id) && getUserStatus(id) == 'Focus mode' && (
                    <span style={{marginRight: "7px"}}>🙅‍♀️</span>
                  )}

                  { getUserStatus(id) && getUserStatus(id) == 'Getting lunch' && (
                    <span style={{marginRight: "7px"}}>🍜</span>
                  )}

                  { getUserStatus(id) && getUserStatus(id) == 'In meeting' && (
                    <span style={{marginRight: "7px"}}>📆</span>
                  )}

                  { getUserStatus(id) && getUserStatus(id) == 'Childcare' && (
                    <span style={{marginRight: "7px"}}>🤱</span>
                  )}
                  <span>
                    {getUserStatus(id) == "meeting"
                      ? "In Meeting"
                      : getUserStatus(id).trim().length &&
                        getUserStatus(id) != "online"
                      ? getUserStatus(id)
                      : getUserStatus(id) == "online"
                      ? "Active now"
                      : "Offline"}
                  </span>
                </li>
              </ul>

              {getUserAppIcon(id) && (
                <div className="teammates-current">
                  <h3>Active app:</h3>
                  <div className="teammates-current__file">
                    <div className="teammates-current__icon">
                      <svg className="icon icon-adobe-xd-1">
                        <use href={getUserAppIcon(id)}></use>
                      </svg>
                    </div>
                    <div className="teammates-current__name">
                      <Link
                        to="#app"
                        onClick={() => {
                          goToDoc(id);
                        }}
                      >
                        {" "}
                        {nameSubstring(getAppTitle(id))}{" "}
                      </Link>
                    </div>
                  </div>
                </div>
              )}

              {(!getUserStatus(id) || !getUserAppIcon(id)) && (
                <div className="teammates-current">
                  <h3>Active app:</h3>
                  <div className="teammates-current__file">
                    <div className="teammates-current__name name-padding">
                      <span>No active app</span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>

          <div className="teammates-bar__content">
            <div className="teammates-bar">
              <div
                className="teammates-bar__item"
                onClick={(e) => startAudioCall(id)}
              >
                <div
                  className={`teammates-bar__icon phone ${
                    getUserStatus(id) == "meeting" ||
                    getUserStatus(id) == "Focus mode" ||
                    !getUserStatus(id)
                      ? "disabled"
                      : ""
                  }`}
                >
                  <svg className="icon icon-phone1">
                    <use href={icons + "#icon-phone1"}></use>
                  </svg>
                </div>
                <span>Call</span>
              </div>
              <div
                className="teammates-bar__item"
                onClick={(e) => startVideoCall(id)}
              >
                <div
                  className={`teammates-bar__icon video ${
                    getUserStatus(id) == "meeting" ||
                    getUserStatus(id) == "Focus mode" ||
                    !getUserStatus(id)
                      ? "disabled"
                      : ""
                  }`}
                >
                  <svg className="icon icon-video2">
                    <use href={icons + "#icon-video2"}></use>
                  </svg>
                </div>
                <span>Video</span>
              </div>
              <div
                className="teammates-bar__item"
                onClick={(e) => {
                  startChat(id);
                }}
              >
                <div
                  className={`teammates-bar__icon chat ${
                    isNewMessage(id) ? "active" : ""
                  }`}
                >
                  <svg className="icon icon-message-circle1">
                    <use href={icons + "#icon-message-circle1"}></use>
                  </svg>
                </div>
                <span>Chat</span>
              </div>
              {/* {getUserAppIcon(id) && (
              <div
                className="teammates-bar__item doc"
                onClick={() => {
                  goToDoc(id);
                }}
              >
                {getUserUrl(id) && (
                  <>
                    <div className="teammates-bar__icon ">
                      <svg className="icon icon-link">
                        <use href={icons + "#icon-link"}></use>
                      </svg>
                    </div>
                    <div className="teammates-bar__doc">
                      <span>{getAppTitle(id)}</span>
                    </div>
                  </>
                )}

                {!getUserUrl(id) && (
                  <>
                    <div className="teammates-bar__icon ">
                      <svg className="icon icon-link">
                        <use href={icons + "#right_down_arrow"}></use>
                      </svg>
                    </div>
                    <div className="teammates-bar__doc">
                      <span>{getAppTitle(id)}</span>
                    </div>
                  </>
                )}
              </div>
            )}
            {!getUserAppIcon(id) && (
              <div
                className="teammates-bar__item doc"
              >
                <div className="teammates-bar__doc">
                  <span>No active app</span>
                </div>
              </div>
            )} */}
            </div>
          </div>
        </div>

        <div className="teammates-body__list">
          <div className="teammates-list">
            {avatarurl && (
              <div className="teammates-list__image">
                <img src={avatarurl} alt="Person" />
                <div className="teammates-about__image--active">
                  <svg className={`icon icon-circul ${getUserStatusIcon(id)}`}>
                    <use href={icons + "#icon-circul"}></use>
                  </svg>
                </div>
              </div>
            )}
            {!avatarurl && (
              <div className="teammates-list__image teammates-list__image--name">
                <p>
                  {name.charAt(0)}
                  {lastname.charAt(0)}
                </p>
                <div className="teammates-about__image--active">
                  <svg className={`icon icon-circul ${getUserStatusIcon(id)}`}>
                    <use href={icons + "#icon-circul"}></use>
                  </svg>
                </div>
              </div>
            )}

            <div className="teammates-list__name">
              <span>
                {name} {lastname}
              </span>
              <span>
                {getUserStatus(id) == "meeting"
                  ? "In Meeting"
                  : getUserStatus(id).trim().length &&
                    getUserStatus(id) != "online"
                  ? getUserStatus(id)
                  : getUserStatus(id) == "online"
                  ? "Active now"
                  : "Offline"}
              </span>
            </div>
            <div className="teammates-list__time">
              {/* <svg className="icon icon-watch">
                <use href={icons + "#icon-watch"}></use>
              </svg> */}
              <span>{time}</span>
            </div>
            {getUserAppIcon(id) && (
              <div className="teammates-current list-app">
                <div className="teammates-current__file">
                  <div className="teammates-current__icon">
                    <svg className="icon icon-adobe-xd-1">
                      <use href={getUserAppIcon(id)}></use>
                    </svg>
                  </div>
                  <div className="teammates-current__name">
                    <Link
                      to="#app"
                      onClick={() => {
                        goToDoc(id);
                      }}
                    >
                      {" "}
                      {nameSubstring(getAppTitle(id))}{" "}
                    </Link>
                  </div>
                </div>
              </div>
            )}

            {(!getUserStatus(id) || !getUserAppIcon(id)) && (
              <div className="teammates-current list-app">
                <div className="teammates-current__file">
                  <div className="teammates-current__name name-padding">
                    <span>No active app</span>
                  </div>
                </div>
              </div>
            )}

            {getUserAppIcon(id) && (
              <div className="teammates-list__document">
                <a
                  href="javascript:void(0)"
                  onClick={() => {
                    goToDoc(id);
                  }}
                >
                  <svg className="icon">
                    <use href={getUserAppIcon(id)}></use>
                  </svg>
                  <span>{getAppTitle(id)}</span>
                </a>
              </div>
            )}

            <button
              className="teammates-list__button talk"
              onClick={(e) => startAudioCall(id)}
            >
              <svg className="icon icon-phone1">
                <use href={icons + "#icon-phone1"}></use>
              </svg>
            </button>
            <button
              className="teammates-list__button video"
              onClick={(e) => startVideoCall(id)}
            >
              <svg className="icon icon-video2">
                <use href={icons + "#icon-video2"}></use>
              </svg>
            </button>
            <button
              className={`teammates-list__button chat`}
              onClick={(e) => {
                startChat(id);
              }}
            >
              <svg className="icon icon-message-circle1">
                <use href={icons + "#icon-message-circle1"}></use>
              </svg>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
