import React, { useEffect, useState } from "react";
import Toastify from 'toastify-js';
import "toastify-js/src/toastify.css";
import Layout from "../../hoc/Layout/Layout";
import Content from "../../hoc/Content/Content";
import "./Rooms.css";
import RoomsItem from "./RoomsItem";
import * as requests from "../../api/requests";
import * as userService from "../../api/users";
import * as teamService from "../../api/teams";
import * as meetingService from "../../api/meeting";

function Rooms() {

  const [rooms, setRooms] = useState([]);
  const [dataLoaded, setDataLoaded] = useState(false);
  let roomEventListener = null, teamEventListener = null;

  useEffect(() => {
    let teamId = teamService.getCurrentTeam().id;
    meetingService
          .getTeamRooms(teamId)
          .then((res) => res.json())
          .then((data) => {
            console.log("rooms: ", data);
            setRooms([...data]);
            setDataLoaded(true);
          })
          .catch((err) => {
            console.log("rooms error: ", err);
          });

     if (teamEventListener == undefined || teamEventListener == null) {
       teamEventListener = window.addEventListener("team-selected", function (
        event
      ) {
       let teamId = teamService.getCurrentTeam().id;
        meetingService
              .getTeamRooms(teamId)
              .then((res) => res.json())
              .then((data) => {
                console.log("rooms: ", data);
                setRooms([...data]);
              })
              .catch((err) => {
                console.log("rooms error: ", err);
              });
            });
     }

    if (roomEventListener == undefined || roomEventListener == null) {
      roomEventListener = window.addEventListener("new-room", function (
        event
      ) {
        meetingService
          .getTeamRooms(teamId)
          .then((res) => res.json())
          .then((data) => {
            console.log("rooms: ", data);
            setRooms([...data]);
          })
          .catch((err) => {
            console.log("rooms error: ", err);
          });
      });
    }

  }, []);

  return (
    <Layout>
      <Content page="Rooms" showcreateroom>
        <div className="rooms">
          <div className="rooms-body">
            <div className="rooms-container">

            {rooms.map((room, i) => (
              <RoomsItem
                key={i}
                id={room.id}
                title={room.title}
                emoji={room.icon}
                host={room.createdBy}
                attandee={room.attandee}
                roomType="green"
               />
             ))}

            {dataLoaded && rooms.length == 0 && (
              <div className="no-scenario">
                <div className="no-scenario-img no-scenario-img-room">
                </div>
              </div>
              )}

            </div>
          </div>
        </div>
      </Content>
    </Layout>
  );
}

export default Rooms;
