import React, { createContext, useState } from "react";

export const InviteContext = createContext();

export function InviteProvider(props) {
  const [invitePeopleShow, setInvitePeopleShow] = useState({
    show: false,
    addPeople: false,
    participants: [],
    hideButton: false
  });
  const showInviteModal = (i, t, id, title, icon,p, h) =>
    setInvitePeopleShow({ show: i, addPeople: t, id: id, title: title, icon: icon, participants: p, hideButton: h});
  return (
    <InviteContext.Provider value={{ invitePeopleShow, showInviteModal }}>
      {props.children}
    </InviteContext.Provider>
  );
}
