import React, { useEffect, useContext, useMemo, useState } from "react";
import Layout from "../../hoc/Layout/Layout";
import Content from "../../hoc/Content/Content";
import "./Notifications.css";
import { TableTheme } from "../../context/TableVariant";
import { SearchTheme } from "../../context/SearchContext";
import NotificationItem from "./NotificationItem";
import * as requests from "../../api/requests";
import * as messagingService from "../../api/messaging";
import * as userService from "../../api/users";

function Notifications(props) {
  let notificationTypes = [
    'user.video-call',
    'user.audio-call',
    'user.request-share',
    'ntf.ht.requestAccepted'
  ];
  const { changeVariant } = useContext(TableTheme);
  const { search } = useContext(SearchTheme);
  const [results, setResults] = useState([]);
  const [notifications, setNotifications] = useState([]);

  useMemo(() => {
    let user = userService.getUserInfo();
    setResults([
      ...notifications.filter((noti) => {
        return (
          noti.actingUser && noti.actingUser.id != user.id && noti.actingUser.name.first
            .toLocaleLowerCase()
            .indexOf(search.toLocaleLowerCase()) !== -1
        );
      }),
    ]);
  }, [search]);

  useEffect(() => {
    changeVariant("apps");
  }, [changeVariant]);

  useEffect(() => {
    let user = userService.getUserInfo();
    messagingService.getNotifications().then((res) => res.json())
      .then((data) => {
        if (Array.isArray(data)) {
          setResults([...data.filter(d => d.actingUser && d.actingUser.id != user.id && d.code && notificationTypes.includes(d.code))]);
          setNotifications([...data.filter(d => d.actingUser && d.actingUser.id != user.id && d.code && notificationTypes.includes(d.code))]);

          let unreadnots = data.filter(d => d.actingUser && !d.readBy.includes(user.id));

          unreadnots.forEach(d => {
            messagingService.readNotification(d.id);
          });
        }
      })
      .catch((err) => {
        console.log("members error: ", err);
      });
  }, []);

  return (
    <Layout>
      <Content
        page="Notifications"
        showTable={false}
        showNotificationDelete
        showInviteButton
      >
        <div className="notification">
          <div className="notification-body">
            <div className="notification-list">
              {results.map((n, i) => (
                <NotificationItem
                  key={i}
                  user={n.actingUser}
                  time={n.createdAt}
                  code={n.code}
                />
              ))}
            </div>
          </div>
        </div>
      </Content>
    </Layout>
  );
}

export default Notifications;
