import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import StLayout from "../StLayout/StLayout";
import Dropdown from "../../../components/UI/Dropdown/Dropdown";
import Layout from "../../../hoc/Layout/Layout";
import * as requests from "../../../api/requests";
import * as teamService from "../../../api/teams";

function CreateTeam() {
  let history = useHistory();

  const values = [
    "Show current app & share app url",
    "Show current app only",
    "No app sharing",
  ];
  const [team, setTeam] = useState('');
  const [onlyAdmin, setOnlyAdmin] = useState(false);
  const [shareApp, setShareApp] = useState(true);
  const [shareAppUrl, setShareAppUrl] = useState(true);

  function selectAppVisibility(value){
    let index = values.indexOf(value);

    switch (index) {
      case 0:
        setShareApp(true);
        setShareAppUrl(true);
        break;
      case 1:
        setShareApp(true);
        setShareAppUrl(false);
        break;
      case 2:
        setShareApp(false);
        setShareAppUrl(true);
        break;
    }
  }

  function createTeam(){
    let pref = {
      onlyAdminInvite: false,
      joinAutomatically: false
    };

    pref.onlyAdminInvite = onlyAdmin;

    if(team && team.trim().length){
      teamService.createTeam(team, pref)
              .then((res) => res.json())
            .then((data) => {
              let teams = teamService.getTeams();

              teams.push(data);
              teamService.setTeams(teams);
              let memberPref = {role: 'teamSuperAdmin', preferences: {shareApp: false, shareAppUrl: false}};
              memberPref.preferences.shareApp = shareApp;
              memberPref.preferences.shareAppUrl = shareAppUrl;
              teamService.createMemberProfile(data.id, memberPref).then((res) => res.json())
              .then((data) => {
                history.push('/dashboard');
              });
            });
    }
    
  }

  return (
    <Layout>
      <StLayout>
        <div className="createteam">
          <div className="createteam-body">
            <div className="createteam-name">
              <h2>What is your team name ?</h2>
              <input type="text" placeholder="My team Name" value={team} onChange={(e) => setTeam(e.target.value)} />
            </div>
            <div className="preferences-choose">
              <div className="preferences-choose__head">
                <div className="preferences-choose__info">
                  <span>Restrict team invites to admins only ?</span>
                </div>
                <label className="preferences-checkbox">
                  <input type="checkbox" hidden checked={onlyAdmin} onChange={() => setOnlyAdmin(!onlyAdmin)} />
                  <span></span>
                </label>
              </div>
              <div className="preferences-choose__text">
                <span>Off- Anyone on the team cn invite new members</span>
              </div>
            </div>
      {/*      <div className="preferences-choose">
              <div className="preferences-choose__head">
                <div className="preferences-choose__info">
                  <span>
                    {" "}
                    Allow anyone with a verified “company email” to join
                    automatically
                  </span>
                </div>
                <label className="preferences-checkbox">
                  <input type="checkbox" hidden />
                  <span></span>
                </label>
              </div>
              <div className="preferences-choose__text">
                <span>Off- Users must have an invite to join</span>
              </div>
            </div>*/}
            <div className="team-sharing createteam-sharing">
              <div className="team-sharing__left">
                <h3>App sharing visibility</h3>
                <span>What status your teammates see</span>
              </div>
              <div className="team-sharing__right">
                <Dropdown values={values} onValueChanged={(v) => selectAppVisibility(v)} />
              </div>
            </div>
            <div className="createteam-sumbit">
              <button onClick={() => createTeam()}>Create New Team</button>
            </div>
          </div>
        </div>
      </StLayout>
    </Layout>
  );
}

export default CreateTeam;
