import React, { createContext, useState } from "react";

export const TimeDropdown = createContext();

export function TimeDropdownProvider(props) {
  const [tselect, setTselect] = useState("");
  const changeTselect = (v) => setTselect(v);
  return (
    <TimeDropdown.Provider value={{ tselect, changeTselect }}>
      {props.children}
    </TimeDropdown.Provider>
  );
}
