import React, { useState, useEffect } from "react";
import "./Header.css";
import Dropdown from "../UI/Dropdown/Dropdown";
import Status from "../UI/Status/Status";
import Notifications from "../UI/Notifications/Notifications";
import * as teamService from "../../api/teams";

function Header() {
  const [teams, setTeams] = useState([]);

  useEffect(() => {
    teamService
      .getUserTeams()
      .then((res) => res.json())
      .then((tms) => {
        console.log("teams: ", tms);
        setTeams([...tms.map((t) => t.title)]);
        teamService.setTeams(tms);
        if(!teamService.getCurrentTeam() || !teamService.getCurrentTeam().title){
          teamService.setCurrentTeam(tms[0].title);
        }
        
      })
      .catch((err) => {
        console.log("members error: ", err);
      });
  }, []);

  function selectCurrentTeam(value) {
    teamService.setCurrentTeam(value);
  }

  function getCurrentTeam(){
    return teamService.getCurrentTeam().title;
  }

  return (
    <header className="header">
      <div className="header-body">
        <div className="header-content">
          <div className="header-content__body">
            <Dropdown
              svalue={getCurrentTeam()}
              values={teams}
              onValueChanged={(v) => selectCurrentTeam(v)}
            />
            <div className="header-content__aside">
              <Notifications />
              <Status />
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}

export default Header;
