import React, { useState, useEffect, useRef } from "react";
import "./Notifications.css";
import icons from "../../../assets/image/icons.svg";
import person from "../../../assets/image/other/person.png";
import { Link } from "react-router-dom";
import * as requests from "../../../api/requests";
import * as messagingService from "../../../api/messaging";
import * as userService from "../../../api/users";
import * as fileService from "../../../api/files";

function Notifications() {
  const [open, setOpen] = useState(false);

  const ref = useRef(null);

  const [notifications, setNotifications] = useState([]);
  const [avatars, setAvatars] = useState({});

  let eventListener;

  useEffect(() => {
    setTimeout(() => {
      let user = userService.getUserInfo();
      if (user && user.id) {
        messagingService.getNotifications().then((res) => res.json())
          .then((data) => {
            if (Array.isArray(data)) {
              let unreadnots = data.filter(d => d.actingUser && !d.readBy.includes(user.id));

              let users = unreadnots.map(d => d.actingUser);
              users = users.filter((u, i) => users.findIndex(us => us && u && us.id == u.id) == i);

              users.forEach(u => {
                if (u.avatar) {
                  fileService.getFile(u.avatar).then((res) => res.json())
                    .then((data) => {
                      avatars[u.id] = data;
                      setAvatars({ ...avatars });
                    });
                } else {
                  avatars[u.id] = undefined;
                  setAvatars({ ...avatars });
                }
              });

              setNotifications([...unreadnots.filter(n => n.actingUser && n.actingUser.id != user.id)]);

              unreadnots.forEach(d => {
                messagingService.readNotification(d.id);
              });
            }
          })
          .catch((err) => {
            console.log("members error: ", err);
          });
      }

    }, 2000);


    if (eventListener == undefined || eventListener == null) {
      eventListener = window.addEventListener("new-notification-for-header", function (event) {
        console.log('new notifications: ', event.detail);
        let data = event.detail;
        if (data.type == 7) {
          let allNots = [...notifications];
          allNots.push(data.data);
          setNotifications([...allNots]);

          messagingService.readNotification(data.data.id);
        }

      });
    }
  }, []);

  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setOpen(false);
      }
    }
    document.addEventListener("click", handleClickOutside);
    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, [ref]);

  function getNotificationType(i) {
    let code = notifications[i].code;
    let type = '';
    switch (code) {
      case 'user.video-call':
        // statements_1
        type = 'Video call';
        break;
      case 'user.audio-call':
        // statements_1
        type = 'Audio call';
        break;
      case 'user.request-share':
        // statements_1
        type = 'Share request';
        break;
      case 'ntf.ht.requestAccepted':
        type = 'New team member ' + notifications[i].actingUser.name.first;
        break;
    }

    return type;
  }

  function getNotificationTime(i) {
    let time = notifications[i].createdAt;
    let d = new Date(time);
    return (d.getHours() < 10 ? '0' + d.getHours() : d.getHours()) + ':' + (d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes());
  }

  return (
    <div className="notifications" ref={ref} style={{ marginLeft: "60px" }}>
      <div className="notifications-body">
        <div className={`notifications-toggle ${notifications.length ? "active" : ""}`} onClick={() => setOpen(!open)}>
          <svg className="icon icon-bell1">
            <use href={icons + "#icon-bell1"}></use>
          </svg>
        </div>
        <div className={`notifications-content ${open ? "open" : ""}`}>
          <div className="notifications-content__body">
            <ul className="notifications-list">
              {notifications.map((n, i) => (
                <>
                  {getNotificationType(i) && (
                    <li className="notifications-item">
                      <div className="notifications-item__person">
                        {avatars[n.actingUser.id] && (
                          <img src={avatars[n.actingUser.id]} alt="Person" />
                        )}
                        {!avatars[n.actingUser.id] && (
                          <p>
                            {n.actingUser.name.first.charAt(0)}
                            {n.actingUser.name.last.charAt(0)}
                          </p>
                        )}
                        <span>{n.actingUser.name.first}</span>
                      </div>
                      <div className="notifications-item__about">
                        <svg className="icon icon-call-missed">
                          <use href={icons + "#icon-call-missed"}></use>
                        </svg>
                        <span>{getNotificationType(i)}</span>
                      </div>
                      <div className="notifications-item__time">
                        <span>{getNotificationTime(i)}</span>
                      </div>
                    </li>
                  )}
                </>
              ))}
            </ul>
          </div>
          <div className="notifications-content__more">
            <Link to="/notifications">see all</Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Notifications;
