import React, { useState, useContext, useEffect } from "react";
import "./AddMeeting.css";
import icons from "../../../assets/image/icons.svg";
import DatePicker from "react-datepicker";
import person from "../../../assets/image/other/person2.png";
import { AddMeetingContext } from "../../../context/AddMeetingContext";
import Toastify from "toastify-js";
import { EmojiButton } from "@joeattardi/emoji-button";
import * as meetingService from "../../../api/meeting";
import * as userService from "../../../api/users";
import * as teamService from "../../../api/teams";
import * as fileService from "../../../api/files";

function AddMeeting() {
  const { addMeeting, showAddMeeting } = useContext(AddMeetingContext);

  const [startDate, setStartDate] = useState(new Date());
  const [startTime, setStartTime] = useState(new Date());
  const [startToDate, setStartToDate] = useState(new Date());
  const [startToTime, setStartToTime] = useState(new Date());
  const [inviteActive, setInvicteActive] = useState(false);
  const [addActive, setAddActive] = useState(false);
  const [meetingId, setMeetingId] = useState("");
  const [meetingTitle, setMeetingTitle] = useState("");
  const [meetingIcon, setMeetingIcon] = useState("");
  const [attandee, setAttandee] = useState([]);
  const [emails, setEmails] = useState([]);
  const [shownMembers, setShownMembers] = useState([]);
  const [hostVideo, setHostVideo] = useState(false);
  const [partVideo, setPartVideo] = useState(false);

  const [emojiChoose, setEmojiChoose] = useState(null);
  useEffect(() => {
    setTimeout(() => {
      console.log("setup emoji!!!");
      const titleEmoji = new EmojiButton({
        // position: {
        //   top: `65px`,
        //   left: `0px`,
        // },
        position: "bottom-start",
        strategy: "absolute",
        showVariants: true,
        emojiSize: "16px",
        showPreview: false,
      });
      const trigger = document.querySelector("#title-trigger");

      titleEmoji.on("emoji", (selection) => {
        setEmojiChoose(selection.emoji);
        setMeetingIcon(selection.emoji);
      });

      trigger.addEventListener("click", () => {
        titleEmoji.togglePicker(trigger);
      });
    });

    if (addMeeting.id) {
      console.log("add meeting obj: ", addMeeting);
      setMeetingId(addMeeting.id);
      setMeetingTitle(addMeeting.title);
      setMeetingIcon(addMeeting.emoji);
      setEmojiChoose(addMeeting.emoji);
      setAttandee([
        ...addMeeting.attandee.filter((a) => a.userId && a.userId.length),
      ]);
      setEmails([
        ...addMeeting.attandee
          .filter((a) => !a.userId && a.email)
          .map((p) => p.email),
      ]);
      setStartDate(new Date(addMeeting.startTime));
      setStartTime(new Date(addMeeting.startTime));
      setStartToTime(new Date(addMeeting.endTime));
      setHostVideo(addMeeting.preferences.hostVideo);
      setPartVideo(addMeeting.preferences.partVideo);
    }
  }, []);

  const eventClick = (e) => {
    e.preventDefault();
  };

  function addRoom() {
    let userId = userService.getUserInfo().id;
    let teamId = teamService.getCurrentTeam().id;
    let meetingStart = new Date(startDate);
    let meetingStartTime = new Date(startTime);
    meetingStart.setHours(meetingStartTime.getHours());
    meetingStart.setMinutes(meetingStartTime.getMinutes());
    let meetingEnd = new Date(startDate);
    let meetingEndTime = new Date(startToTime);
    meetingEnd.setHours(meetingEndTime.getHours());
    meetingEnd.setMinutes(meetingEndTime.getMinutes());

    let attandees = [...attandee];

    emails.forEach((e) => {
      attandees.push({ email: e, name: e.split("@")[0] });
    });

    let meeting = {
      title: meetingTitle,
      icon: meetingIcon,
      createdBy: userId,
      teamId: teamId,
      attandee: attandees,
      startTime: meetingStart,
      endTime: meetingEnd,
      preferences: {
        hostVideo: hostVideo,
        partVideo: partVideo,
      },
    };
    console.log("meeting: ", meeting);
    if (meetingTitle && meetingTitle.trim().length && attandees.length) {
      if (addMeeting.id) {
        meetingService
          .updateMeeting(addMeeting.id, meeting)
          .then((res) => res.json())
          .then((data) => {
            console.log("room updated: ", data);
            window.dispatchEvent(
              new CustomEvent("new-meeting", {
                detail: data,
              })
            );
            showAddMeeting(false);
          })
          .catch((err) => {
            console.log("room error: ", err);
          });
      } else {
        meetingService
          .scheduleMeeting(meeting)
          .then((res) => res.json())
          .then((data) => {
            console.log("room added: ", data);
            window.dispatchEvent(
              new CustomEvent("new-meeting", {
                detail: data,
              })
            );
            showAddMeeting(false);
          })
          .catch((err) => {
            console.log("room error: ", err);
          });
      }
    }
  }

  function searchMembers(el) {
    console.log("name: ", el.value);
    setTimeout(() => {
      let name = el.value.toString();
      if (name && name.trim().length) {
        let userId = userService.getUserInfo().id;
        let members = teamService.getMembers().filter((m) => m.id != userId);
        console.log("members11: ", members);
        members.forEach((m) => {
          if (m.avatar) {
            m.avatarUrl = fileService.getFileUrl(m.avatar);
          }
        });
        members = members.filter((m) =>
          (m.name.first + " " + m.name.last)
            .toLowerCase()
            .includes(name.trim().toLowerCase())
        );
        console.log("members22: ", members);
        setShownMembers([...members]);
      } else {
        setShownMembers([]);
      }
    });
  }

  function addMember(member) {
    let members = [...attandee];
    if (!members.find((m) => m.userId == member.id)) {
      let mem = {
        userId: member.id,
        name: member.name.first + " " + member.name.last,
        email: member.email,
      };

      if (member.avatar && member.avatarUrl) {
        mem.avatar = member.avatar;
        mem.avatarUrl = member.avatarUrl;
      }

      members.push(mem);
      setAttandee([...members]);
    }
    setShownMembers([]);
    document.querySelector("input[name='searchInput']").value = "";
  }

  function removeMember(member) {
    let members = [...attandee];
    if (members.find((m) => m.userId == member.userId)) {
      members = members.filter((m) => m.userId != member.userId);
      setAttandee([...members]);
    }
  }

  function addEmail(e) {
    if (e.keyCode == 13) {
      let email = e.target.value;
      let regex = new RegExp(
        /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );
      if (
        email &&
        email.trim().length &&
        regex.test(email.trim().toLowerCase())
      ) {
        let ems = [...emails];
        if (!ems.includes(email.trim().toLowerCase())) {
          ems.push(email.trim().toLowerCase());
          setEmails([...ems]);
        }
        e.target.value = "";
      }
    }
  }

  function removeEmail(email) {
    let ems = [...emails];
    if (ems.includes(email)) {
      ems = ems.filter((m) => m != email);
      setEmails([...ems]);
    }
  }

  return (
    <div className="addmeeting">
      <div className="addmeeting-body">
        <div className="addmeeting-modal">
          <div
            className="addmeeting-modal__close"
            onClick={() => showAddMeeting(false)}
          >
            <svg className="icon icon-cancel">
              <use href={icons + "#icon-cancel"}></use>
            </svg>
          </div>
          <div className="addmeeting-header">
            <h2>Add meeting</h2>
          </div>
          <div id="title-emoji"></div>
          <div className="rooms-modal__head">
            {/* <h2>Create Room</h2> */}
            <button
              id="title-trigger"
              type="button"
              className="crmeeting-form__emoji"
            >
              {emojiChoose ? (
                emojiChoose
              ) : (
                <svg className="icon icon-emoji">
                  <use href={icons + "#icon-emoji"}></use>
                </svg>
              )}
              <span>Add icon</span>
            </button>
          </div>
          <div className="addmeeting-name">
            <input
              type="text"
              placeholder="Meeting name"
              value={meetingTitle}
              onChange={(e) => setMeetingTitle(e.target.value)}
            />
          </div>
          <div className="addmeeting-date">
            <h3 className="addmeeting-title">Meeting time</h3>
            <div className="addmeeting-date__container">
              <div className="addmeeting-date__picker">
                <div className="addmeeting-date__input">
                  <DatePicker
                    selected={startDate}
                    onChange={(date) => setStartDate(date)}
                  />
                  <svg className="icon icon-down-abgle">
                    <use href={icons + "#icon-down-abgle"}></use>
                  </svg>
                </div>
                <div className="addmeeting-date__input">
                  <DatePicker
                    selected={startTime}
                    onChange={(date) => setStartTime(date)}
                    showTimeSelect
                    showTimeSelectOnly
                    timeIntervals={1}
                    timeCaption="Time"
                    dateFormat="h:mm aa"
                  />
                  <svg className="icon icon-down-abgle">
                    <use href={icons + "#icon-down-abgle"}></use>
                  </svg>
                </div>
              </div>
              <span className="addmeeting-date__to">to</span>
              <div className="addmeeting-date__picker">
                {/*                <div className="addmeeting-date__input">
                  <DatePicker
                    selected={startToDate}
                    onChange={(date) => setStartToDate(date)}
                  />
                  <svg className="icon icon-down-abgle">
                    <use href={icons + "#icon-down-abgle"}></use>
                  </svg>
                </div>*/}
                <div className="addmeeting-date__input">
                  <DatePicker
                    selected={startToTime}
                    onChange={(date) => setStartToTime(date)}
                    showTimeSelect
                    showTimeSelectOnly
                    timeIntervals={1}
                    timeCaption="Time"
                    dateFormat="h:mm aa"
                  />
                  <svg className="icon icon-down-abgle">
                    <use href={icons + "#icon-down-abgle"}></use>
                  </svg>
                </div>
              </div>
            </div>
          </div>
          <div className="addmeeting-video">
            <h3 className="addmeeting-title">Video</h3>
            <div className="addmeeting-video__container">
              <div className="addmeeting-video__type">
                <span className="addmeeting-video__type-title">Host</span>
                <div className="addmeeting-video__radiobtn">
                  <div className="radiobtn">
                    <label className="radiobtn-label">
                      <input
                        type="radio"
                        name="host"
                        checked={hostVideo}
                        hidden
                        onChange={() => {
                          setHostVideo(true);
                        }}
                      />
                      <span className="radiobtn-circul"></span>
                      <span className="radiobtn-title">On</span>
                    </label>
                  </div>
                  <div className="radiobtn">
                    <label className="radiobtn-label">
                      <input
                        type="radio"
                        name="host"
                        checked={!hostVideo}
                        hidden
                        onChange={() => {
                          setHostVideo(false);
                        }}
                      />
                      <span className="radiobtn-circul"></span>
                      <span className="radiobtn-title">Off</span>
                    </label>
                  </div>
                </div>
              </div>
              <div className="addmeeting-video__type">
                <span className="addmeeting-video__type-title">
                  Participants
                </span>
                <div className="addmeeting-video__radiobtn">
                  <div className="radiobtn">
                    <label className="radiobtn-label">
                      <input
                        type="radio"
                        name="Participants"
                        checked={partVideo}
                        hidden
                        onChange={() => {
                          setPartVideo(true);
                        }}
                      />
                      <span className="radiobtn-circul"></span>
                      <span className="radiobtn-title">On</span>
                    </label>
                  </div>
                  <div className="radiobtn">
                    <label className="radiobtn-label">
                      <input
                        type="radio"
                        name="Participants"
                        checked={!partVideo}
                        hidden
                        onChange={() => {
                          setPartVideo(false);
                        }}
                      />
                      <span className="radiobtn-circul"></span>
                      <span className="radiobtn-title">Off</span>
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="addmeeting-calendar">
            <h3 className="addmeeting-title">Calendar</h3>
            <div className="addmeeting-calendar__container">
              <div className="radiobtn">
                <label className="radiobtn-label">
                  <input type="radio" name="calendar" hidden />
                  <span className="radiobtn-circul"></span>
                  <span className="radiobtn-title">Google calendar</span>
                </label>
              </div>
              <div className="radiobtn">
                <label className="radiobtn-label">
                  <input type="radio" name="calendar" checked hidden />
                  <span className="radiobtn-circul"></span>
                  <span className="radiobtn-title">Other calendar</span>
                </label>
              </div>
            </div>
          </div>
          <div className="addmeeting-people">
            <div className="addmeeting-people__header">
              <h3 className="addmeeting-title">People</h3>
              <button
                className={`addmeeting-people__invite ${
                  inviteActive ? "active" : ""
                }`}
                onClick={() => {
                  setInvicteActive(!inviteActive);
                  setAddActive(false);
                }}
              >
                Invite with email
              </button>
            </div>
            <ul className="addmeeting-people__list">
              {attandee.map((member, i) => (
                <li className="addmeeting-people__item">
                  {member.avatarUrl && (
                    <img src={member.avatarUrl} alt="Person" />
                  )}
                  {!member.avatarUrl && <p>{member.name.charAt(0)}</p>}
                  <span
                    className="addmeeting-people__remove"
                    onClick={(e) => removeMember(member)}
                  >
                    <svg className="icon icon-close">
                      <use href={icons + "#icon-close"}></use>
                    </svg>
                  </span>
                </li>
              ))}
            </ul>
          </div>
          <div className="addmeeting-person">
            <button
              className={`addmeeting-person__add ${addActive ? "active" : ""}`}
              onClick={() => {
                setAddActive(!addActive);
                setInvicteActive(false);
              }}
            >
              Add person
            </button>

            {addActive && (
              <div className="addmeeting-person__search">
                <input
                  type="text"
                  placeholder="Search members"
                  name="searchInput"
                  onKeyDown={(e) => searchMembers(e.target)}
                />
                <button>
                  <svg className="icon icon-search">
                    <use href={icons + "#icon-search"}></use>
                  </svg>
                </button>

                <div className="addmeeting-person__search__list">
                  {shownMembers.map((member, i) => (
                    <div
                      className="addmeeting-person__search__list__item"
                      onClick={(e) => addMember(member)}
                    >
                      {member.avatarUrl && (
                        <div className="addmeeting-person__search__list__item_image">
                          <img src={member.avatarUrl} alt="Person" />
                        </div>
                      )}
                      {!member.avatarUrl && (
                        <div className="addmeeting-person__search__list__item__image addmeeting-person__search__list__item__image--name">
                          <p>
                            {member.name.first.charAt(0)}
                            {member.name.last.charAt(0)}
                          </p>
                        </div>
                      )}
                      <div className="name">
                        {member.name.first} {member.name.last}
                      </div>
                      <div style={{ clear: "both" }}></div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {inviteActive && (
              <div className="addmeeting-person__search">
                <input
                  type="text"
                  placeholder="Enter person email"
                  onKeyDown={(e) => addEmail(e)}
                />
                <button>
                  <svg className="icon icon-plus-circle ">
                    <use href={icons + "#icon-plus-circle"}></use>
                  </svg>
                </button>
              </div>
            )}

            {emails.map((email, i) => (
              <div className="addmeeting-person__email">
                <a href="#">{email} </a>
                <span
                  className="addmeeting-people__remove"
                  onClick={(e) => removeEmail(email)}
                >
                  <svg className="icon icon-close">
                    <use href={icons + "#icon-close"}></use>
                  </svg>
                </span>
              </div>
            ))}
          </div>
          <div className="addmeeting-submit">
            <button onClick={(e) => addRoom()}>
              {addMeeting.id ? "Update" : "Save"}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AddMeeting;
