import React, { useState, useEffect, useRef, useContext } from "react";
import { InviteContext } from "../../../context/InvitePeopleContext";
import icons from "../../../assets/image/icons.svg";
import "../Meeting.css";
import Toastify from 'toastify-js';
import "toastify-js/src/toastify.css";
import { Link } from "react-router-dom";
import person1 from "../../../assets/image/other/person.png";
import { AddMeetingContext } from "../../../context/AddMeetingContext";
import * as requests from "../../../api/requests";
import * as userService from "../../../api/users";
import * as meetingService from "../../../api/meeting";
import * as filesService from "../../../api/files";
import { CopyToClipboard } from "react-copy-to-clipboard";
const electron = window.require("electron").remote;

function MeetingItem({
  id,
  title,
  emoji,
  attandee,
  meetType,
  startTime,
  endTime,
  preferences,
  host,
}) {
  const { showInviteModal } = useContext(InviteContext);

  const [open, setOpen] = useState(false);
  const [show, setShow] = useState(false);
  const [updateDom, setUpdateDom] = useState(false);
  const { addMeeting, showAddMeeting } = useContext(AddMeetingContext);
  const ref = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setShow(false);
      }
    }
    document.addEventListener("click", handleClickOutside);
    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, [ref]);

  useEffect(() => {
    attandee.forEach((att) => {
      if (att.avatar) {
        let url = filesService.getFileUrl(att.avatar);
        if (url) {
          att.avatarUrl = url;
          setUpdateDom(!updateDom);
        } else {
          filesService
            .getFile(att.avatar)
            .then((res) => res.json())
            .then((data) => {
              filesService.saveFile({ id: att.avatar, url: data });
              att.avatarUrl = url;
              setUpdateDom(!updateDom);
            });
        }
      }
    });
  }, [attandee]);

  function isHost() {
    let userInfo = userService.getUserInfo();
    return userInfo.id == host;
  }

  function isEnded() {
    let now = new Date().getTime();
    let end = new Date(endTime).getTime();

    return now > end;
  }

  function startMeeting() {
    let user = userService.getUserInfo();
    let host = isHost();
    if (host) {
      meetingService.startMeeting(id);
    }
    let path = electron.getGlobal("sharedPath");
    let filePath = electron.getGlobal("filePath");
    const BrowserWindow = electron.BrowserWindow;
    let callWindow = new BrowserWindow({
      width: 300,
      height: 306,
      title: "Wteam meeting",
      frame: false,
      transparent: true,
      resizable: false,
      fullscreen: false,
      webPreferences: {
        preload: filePath + "../public/preload.js",
        nodeIntegration: true,
        enableRemoteModule: true,
        webSecurity: false,
      },
    });
    callWindow.setFullScreenable(true);
    callWindow.loadURL(
      path +
        "../call/call.html?meeting=" +
        id +
        "&fromId=" +
        user.id +
        "&token=" +
        requests.getToken()
    );
  }

  function cancelMeeting() {
    meetingService
      .deleteMeeting(id)
      .then((res) => res.json())
      .then((data) => {
        window.dispatchEvent(
          new CustomEvent("new-meeting", {
            detail: data,
          })
        );
        setOpen(false);
      })
      .catch((err) => {
        console.log("room error: ", err);
      });
  }

  function convertStartTime() {
    return new Date(startTime).toLocaleString();
  }

  function convertEndTime() {
    return new Date(endTime).toLocaleTimeString();
  }

  function getMeetingUrl() {
    return "https://meeting.wteam.chat?meeting=" + id;
  }

  function addNoti(){
    Toastify({
      text: "Link copied!",
      duration: 3000,
      close: true,
      gravity: "bottom", // `top` or `bottom`
      position: "right", // `left`, `center` or `right`
      backgroundColor: "linear-gradient(to right, #00b09b, #96c93d)",
      stopOnFocus: true, // Prevents dismissing of toast on hover
    }).showToast();
  }

  return (
    <div className={`meeting-content ${meetType}`}>
      <div className="meeting-content__head">
        <div className="meeting-content__emojy">
          <span role="img" aria-label={emoji}>
            {emoji}
          </span>
        </div>
        <div className="meeting-content__head-title">
          <h2>{title}</h2>
          <span>
            {convertStartTime()} - {convertEndTime()} {isEnded() && "(expired)"}
          </span>
        </div>

        <button>
          <svg className="icon icon-volume-1">
            <use href={icons + "#icon-volume-1"}></use>
          </svg>
        </button>
        {isHost() && (
          <>
            <button onClick={() => setOpen(!open)}>
              <svg className="icon icon-dost">
                <use href={icons + "#icon-dost"}></use>
              </svg>
            </button>
          </>
        )}
        {open && (
          <div className="meeting-tools">
            <div className="meeting-tools__body">
              <div className="meeting-tools__content">
                <p
                  onClick={() => {
                    showAddMeeting(
                      true,
                      id,
                      title,
                      emoji,
                      attandee,
                      startTime,
                      endTime,
                      preferences
                    );
                    setOpen(!open);
                  }}
                >
                  <svg className="icon icon-edit">
                    <use href={icons + "#icon-edit"}></use>
                  </svg>
                  <span>Edit</span>
                </p>
                <p onClick={() => cancelMeeting()}>
                  <svg className="icon icon-cancel">
                    <use href={icons + "#icon-cancel"}></use>
                  </svg>
                  <span>Cancel</span>
                </p>
              </div>
            </div>
          </div>
        )}
      </div>

      <div className="meeting-content__flex">
        <div className="meeting-content__bottom">
          <h2 className="meeting-content__title">Invite people</h2>
          <div className="meeting-content__images">
            {attandee.map((att, i) => (
              <>
                {att && att.avatar && att.avatarUrl && i < 3 && (
                  <div className="meeting-content__images-position">
                    <img src={att.avatarUrl} alt="" />
                  </div>
                )}

                {att && !att.avatarUrl && att.name && i < 3 && (
                  <div className="meeting-content__images-position">
                    <p>{att.name.charAt(0)}</p>
                  </div>
                )}
              </>
            ))}
            <div
              className="meeting-content__more meeting-content__images-position"
              onClick={() =>
                showInviteModal(true, true, id, title, emoji, attandee, false)
              }
            >
              <span>+</span>
            </div>
            <div className="rooms-content__count meeting-content__count">
              <span
                onClick={() =>
                  showInviteModal(
                    true,
                    false,
                    id,
                    title,
                    emoji,
                    attandee,
                    false
                  )
                }
              >
                See {attandee.length} people
              </span>
            </div>
          </div>
        </div>

        <div className="meeting-join">
          <div className="meeting-join__link">
            <CopyToClipboard text={getMeetingUrl()} onCopy={() => {addNoti();}}>
                  <span>
                    <svg className="icon icon-copy">
                      <use href={icons + "#icon-copy"}></use>
                    </svg>
                  </span>
                </CopyToClipboard>
            <input value={getMeetingUrl()} readonly="readonly" />
          </div>
          {!isEnded() && (
            <button onClick={() => startMeeting()}>
              {isHost() ? "Start" : "Join"}
            </button>
          )}
        </div>
      </div>
    </div>
  );
}

export default MeetingItem;
