import React, { useState, useEffect } from "react";
import Layout from "../../../hoc/Layout/Layout";
import Content from "../../../hoc/Content/Content";
import MeetingItem from "../Meeting/MeetingItem";
import "../Meeting.css";
import * as requests from "../../../api/requests";
import * as userService from "../../../api/users";
import * as teamService from "../../../api/teams";
import * as meetingService from "../../../api/meeting";

function Meeting() {

  const [meetings, setMeetings] = useState([]);
  const [dataLoaded, setDataLoaded] = useState(false);
  let meetingEventListener = null, teamEventListener = null;

  useEffect(() => {
    let teamId = teamService.getCurrentTeam().id;
    meetingService
    .getMemberMeetings(teamId)
    .then((res) => res.json())
    .then((data) => {
      console.log("meetings: ", data);
      setMeetings([...data]);
      setDataLoaded(true);
    })
    .catch((err) => {
      console.log("rooms error: ", err);
    });

    if (teamEventListener == undefined || teamEventListener == null) {
      teamEventListener = window.addEventListener("team-selected", function (
        event
      ) {
       let teamId = teamService.getCurrentTeam().id;
        meetingService
        .getMemberMeetings(teamId)
        .then((res) => res.json())
        .then((data) => {
          console.log("meetings: ", data);
          setMeetings([...data]);
        })
        .catch((err) => {
          console.log("rooms error: ", err);
        });
      });
     }

    if (meetingEventListener == undefined || meetingEventListener == null) {
      meetingEventListener = window.addEventListener("new-meeting", function (
        event
        ) {
        let teamId = teamService.getCurrentTeam().id;
        meetingService
        .getMemberMeetings(teamId)
        .then((res) => res.json())
        .then((data) => {
          console.log("meetings: ", data);
          setMeetings([...data]);
        })
        .catch((err) => {
          console.log("rooms error: ", err);
        });
      });
    }
  }, []);


  return (
    <Layout>
    <Content page="Meeting" showcreatemeeting>
    <div className="meeting">
    <div className="meeting-body">
    <div className="meeting-container">
    {meetings.map((meeting, i) => (
      <MeetingItem
      key={i}
      id={meeting.id}
      title={meeting.title}
      emoji={meeting.icon}
      attandee={meeting.attandee}
      startTime={meeting.startTime}
      endTime={meeting.endTime}
      preferences={meeting.preferences}
      host={meeting.createdBy}
      meetType="green"
      />
      ))}
    {dataLoaded && meetings.length == 0 && (
    <div className="no-scenario">
      <div className="no-scenario-img no-scenario-img-meeting">
      </div>
    </div>
    )}
    </div>
    </div>
    </div>
    </Content>
    </Layout>
    );
}

export default Meeting;
