import * as requests from './requests';
import {config} from './../config';

let activeCall = '', activeMeeting = '', activeRoom = '', isInMeeting = false;

export function setInMeeting(){
	isInMeeting = true;
}

export function setNotInMeeting(){
	isInMeeting = false;
}

export function getInMeeting(){
	return isInMeeting;
}

export function getTeamMeetings(teamId)
{
	return requests.getAuth(config.services.meeting + 'meeting/' + teamId + '/team-meetings');
}

export function getMemberMeetings(teamId)
{
	return requests.getAuth(config.services.meeting + 'meeting/' + teamId + '/member-meetings');
}

export function getMeeting(meetingId)
{
	return requests.getAuth(config.services.meeting + 'meeting/' + meetingId);
}

export function scheduleMeeting(meeting)
{
	return requests.postAuth(config.services.meeting + 'meeting', meeting);
}

export function updateMeeting(meetingId, meeting)
{
	return requests.putAuth(config.services.meeting + 'meeting/' + meetingId, meeting);
}

export function startMeeting(meetingId)
{
	return requests.putAuth(config.services.meeting + 'meeting/' + meetingId + '/start', undefined);
}

export function updateMeetingMembers(meetingId, members)
{
	return requests.putAuth(config.services.meeting + 'meeting/' + meetingId + '/members', members);
}

export function deleteMeeting(meetingId)
{
	return requests.deleteReqAuth(config.services.meeting + 'meeting/' + meetingId);
}

export function getTeamRooms(teamId)
{
	return requests.getAuth(config.services.meeting + 'room/' + teamId + '/team-rooms');
}

export function getRoom(roomId)
{
	return requests.getAuth(config.services.meeting + 'room/' + roomId);
}

export function addRoom(room)
{
	return requests.postAuth(config.services.meeting + 'room', room);
}

export function updateRoom(roomId, room)
{
	return requests.putAuth(config.services.meeting + 'room/' + roomId, room);
}

export function deleteRoom(roomId)
{
	return requests.deleteReqAuth(config.services.meeting + 'room/' + roomId);
}

export function addMemberToRoom(roomId, member)
{
	return requests.putAuth(config.services.meeting + 'room/' + roomId + '/member', member);
}

export function deleteMemberFromRoom(roomId)
{
	return requests.deleteReqAuth(config.services.meeting + 'room/' + roomId + '/member');
}
