import React, { useState, useEffect, useRef, useContext } from "react";
import icons from "../../assets/image/icons.svg";
import { Link } from "react-router-dom";
import person1 from "../../assets/image/other/person.png";
import person2 from "../../assets/image/other/person2 (1).png";
import person3 from "../../assets/image/other/person3.png";
import Toastify from 'toastify-js';
import "toastify-js/src/toastify.css";

import * as requests from "../../api/requests";
import * as userService from "../../api/users";
import * as filesService from "../../api/files";
import * as meetingService from "../../api/meeting";
import { InviteContext } from "../../context/InvitePeopleContext";
import { EmojiButton } from "@joeattardi/emoji-button";
import { CopyToClipboard } from "react-copy-to-clipboard";

const electron = window.require("electron").remote;

function RoomsItem({ id, title, emoji, host, attandee, roomType }) {
  const { invitePeopleShow, showInviteModal } = useContext(InviteContext);
  const [open, setOpen] = useState(false);
  const [show, setShow] = useState(false);
  const [updateDom, setUpdateDom] = useState(false);
  const ref = useRef(null);

  const [roomTitle, setRoomTitle] = useState(title);
  const [roomIcon, setRoomIcon] = useState(emoji);
  const [roomModalShow, setRoomModalShow] = useState(false);

  const [emojiTitle, setEmojiTitle] = useState(null);
  const [emojiChoose, setEmojiChoose] = useState(emoji);

  useEffect(() => {
    attandee.forEach((att) => {
      if (att.avatar) {
        let url = filesService.getFileUrl(att.avatar);
        if (url) {
          att.avatarUrl = url;
          setUpdateDom(!updateDom);
        } else {
          filesService
            .getFile(att.avatar)
            .then((res) => res.json())
            .then((data) => {
              filesService.saveFile({ id: att.avatar, url: data });
              att.avatarUrl = url;
              setUpdateDom(!updateDom);
            });
        }
      }
    });
  }, [attandee]);

  useEffect(() => {
    if (roomModalShow) {
      setTimeout(() => {
        console.log("setup emoji!!!");
        const titleEmoji = new EmojiButton({
          // position: {
          //   top: `65px`,
          //   left: `0px`,
          // },
          position: "bottom-start",
          strategy: "absolute",
          showVariants: true,
          emojiSize: "16px",
          showPreview: false,
        });
        const trigger = document.querySelector("#title-trigger");

        titleEmoji.on("emoji", (selection) => {
          setEmojiChoose(selection.emoji);
          setEmojiTitle(selection.emoji);
          setRoomIcon(selection.emoji);
        });

        trigger.addEventListener("click", () => {
          titleEmoji.togglePicker(trigger);
        });
      });
    }
  }, [roomModalShow]);

  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setShow(false);
      }
    }
    document.addEventListener("click", handleClickOutside);
    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, [ref]);

  const eventClick = (e) => {
    e.preventDefault();
  };

  function deleteRoom(id) {
    meetingService
      .deleteRoom(id)
      .then((res) => res.json())
      .then((data) => {
        console.log("room added: ", data);
        window.dispatchEvent(
          new CustomEvent("new-room", {
            detail: data,
          })
        );
        setOpen(false);
      })

      .catch((err) => {
        console.log("room error: ", err);
      });
  }

  function joinRoom(id) {
    let user = userService.getUserInfo();
    let path = electron.getGlobal("sharedPath");
    let filePath = electron.getGlobal("filePath");
    const BrowserWindow = electron.BrowserWindow;
    let callWindow = new BrowserWindow({
      width: 300,
      height: 306,
      title: "Wteam room",
      frame: false,
      transparent: true,
      resizable: false,
      fullscreen: false,
      webPreferences: {
        preload: filePath + "../public/preload.js",
        nodeIntegration: true,
        enableRemoteModule: true,
        webSecurity: false,
      },
    });
    callWindow.setFullScreenable(true);
    callWindow.loadURL(
      path +
        "../call/call.html?room=" +
        id +
        "&fromId=" +
        user.id +
        "&token=" +
        requests.getToken()
    );
  }

  function updateRoom(id) {
    let room = {
      title: roomTitle,
      icon: roomIcon,
    };
    meetingService
      .updateRoom(id, room)
      .then((res) => res.json())
      .then((data) => {
        console.log("room added: ", data);
        window.dispatchEvent(
          new CustomEvent("new-room", {
            detail: data,
          })
        );
        setRoomModalShow(false);
      })
      .catch((err) => {
        console.log("room error: ", err);
      });
  }

  function getRoomUrl() {
    return "https://meeting.wteam.chat?room=" + id;
  }

  function isHost() {
    let userInfo = userService.getUserInfo();
    return userInfo.id == host;
  }

  function addNoti(){
    Toastify({
      text: "Link copied!",
      duration: 3000,
      close: true,
      gravity: "bottom", // `top` or `bottom`
      position: "right", // `left`, `center` or `right`
      backgroundColor: "linear-gradient(to right, #00b09b, #96c93d)",
      stopOnFocus: true, // Prevents dismissing of toast on hover
    }).showToast();
  }

  return (
    <div className={`rooms-content ${roomType}`}>
      <div className="rooms-content__head">
        <div className="rooms-content__emojy">
          <span role="img" aria-label="{emoji}">
            {emoji}
          </span>
        </div>

        <h2>{title}</h2>

        <button>
          <svg className="icon icon-volume-1">
            <use href={icons + "#icon-volume-1"}></use>
          </svg>
        </button>
        {isHost() && (
        <button onClick={() => setOpen(!open)}>
          <svg className="icon icon-dost">
            <use href={icons + "#icon-dost"}></use>
          </svg>
        </button>
        )}
        {open && (
          <div className="rooms-tools">
            <div className="rooms-tools__body">
              <div className="rooms-tools__content">
                <p
                  onClick={() => {
                    setRoomModalShow(true);
                    setOpen(false);
                  }}
                >
                  <svg className="icon icon-edit">
                    <use href={icons + "#icon-edit"}></use>
                  </svg>
                  <span>Edit</span>
                </p>
                <p onClick={() => deleteRoom(id)}>
                  <svg className="icon icon-trash-2">
                    <use href={icons + "#icon-trash-2"}></use>
                  </svg>
                  <span>Delete</span>
                </p>
              </div>
            </div>
          </div>
        )}
      </div>

      {/*<div className={`rooms-content__center ${show && "show"}`}>
        <div className="rooms-persons">
          <div className="rooms-persons__hide" onClick={() => setShow(false)}>
            <svg className="icon icon-up-angle">
              <use href={icons + "#icon-up-angle"}></use>
            </svg>
          </div>
          <div className="rooms-persons__body">
            <ul className="rooms-persons__list">
              <li className="rooms-persons__item">
                <img src={person2} alt="" />
                <span>Valeh Huseynov</span>
              </li>
              <li className="rooms-persons__item">
                <img src={person2} alt="" />
                <span>Valeh Huseynov</span>
              </li>
            </ul>
          </div>
        </div>
      </div>*/}

      {/*<div className="rooms-content__bottom" ref={ref}>
          <h2 className="rooms-content__title">Participants</h2>
          <div className="rooms-content__images">
          {attandee.map((att, i) => (
            <>
              {att && att.avatar && att.avatarUrl && (
                <img src={att.avatarUrl} alt="" />
              )}

              {att && !att.avatarUrl && att.name && <p>{att.name.charAt(0)}</p>}
            </>
          ))}
            
          </div>
        </div>*/}

      {/* <div className="rooms-settings">
        <button><svg className="icon icon-users"><use href={icons + "#icon-users"}></use></svg><span>Invite People</span></button>
        <button><svg className="icon icon-content_copy"><use href={icons + "#icon-content_copy"}></use></svg><span>Copy Link</span></button>
      </div> */}

      <div className="rooms-content__invite">
        <div className="rooms-content__bottom" ref={ref}>
          <h2 className="rooms-content__title">Participants</h2>
          <div className="rooms-content__images">
            {attandee.map((att, i) => (
              <>
                {att && att.avatar && att.avatarUrl && i < 3 && (
                  <div className="rooms-content__images-positions">
                    <img src={att.avatarUrl} alt="" />
                  </div>
                )}

                {att && !att.avatarUrl && att.name && i < 3 && (
                  <div className="rooms-content__images-positions">
                    <p>{att.name.charAt(0)}</p>
                  </div>
                )}
              </>
            ))}
            {attandee.length > 3 &&
              <div
                className="rooms-content__more rooms-content__images-positions"
                onClick={() =>
                  showInviteModal(true, false, id, title, emoji, attandee, true)
                }
              >
                 <span>+ {attandee.length - 3}</span>
              </div>
            }
          </div>
        </div>
 {/*       <div className="rooms-content__count">
          <span
            onClick={() =>
              showInviteModal(true, false, id, title, emoji, attandee, true)
            }
          >
            See {attandee.length > 3 ? attandee.length - 3 : ""} people
          </span>
        </div>*/}
      </div>

      <div className="rooms-join">
        <div className="rooms-join__link">
          
        <CopyToClipboard text={getRoomUrl()} onCopy={() => {addNoti();}}>
                  <span>
                    <svg className="icon icon-copy">
                      <use href={icons + "#icon-copy"}></use>
                    </svg>
                  </span>
                </CopyToClipboard>
          <input value={getRoomUrl()} readonly="readonly" />
        </div>
        <button onClick={() => joinRoom(id)}> Join Room</button>
      </div>

      {roomModalShow && (
        <div className="rooms-modal">
          <div className="rooms-modal__body">
            <div className="rooms-modal__content">
              <button
                className="rooms-modal__close"
                onClick={() => setRoomModalShow(false)}
              >
                <svg className="icon icon-cancel">
                  <use href={icons + "#icon-cancel"}></use>
                </svg>
              </button>
              <div id="title-emoji"></div>
              <div className="rooms-modal__head">
                {/* <h2>Create Room</h2> */}
                <button
                  id="title-trigger"
                  type="button"
                  className="crmeeting-form__emoji"
                >
                  {emojiChoose ? (
                    emojiChoose
                  ) : (
                    <svg className="icon icon-emoji">
                      <use href={icons + "#icon-emoji"}></use>
                    </svg>
                  )}
                  <span>Add icon</span>
                </button>
              </div>
              <div className="crmeeting-form__group">
                {/* <label>Title</label> */}
                <div id="emoji-content" className="crmeeting-form__title">
                  <input
                    type="text"
                    placeholder="Add Title"
                    value={roomTitle}
                    onChange={(e) => setRoomTitle(e.target.value)}
                  />
                </div>
              </div>
              <div className="crmeeting-form__submit">
                <button onClick={(e) => updateRoom(id)}>Save</button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default RoomsItem;
